<template>
    <div>
        <div v-if="!httpLoad.process && httpLoad.access && !enable_panel_login" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                Maintenance Mode.
            </div>
        </div>

        <div v-if="httpLoad.process" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="!httpLoad.process && !httpLoad.access" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="httpLoad.access">

            <div class="page-header">
                <h2>News and Updates - Create</h2>
            </div>

            <div class="row">
                <side-menu></side-menu>
                <div class="col-md-9">

                    <div class="panel panel-default">
                        <div class="panel-heading">
                            <h3 class="panel-title">Create New Post</h3>
                        </div>
                        <div class="panel-body">

                            <div v-if="httpRequest.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="success">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <form v-on:submit.prevent="">

                                <div class="form-group">
                                    <div class="checkbox">
                                        <label>
                                            <input type="checkbox" v-model="post.is_public"> Public Post
                                        </label>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <div class="checkbox">
                                        <label>
                                            <input type="checkbox" v-model="post.pinned"> Pin Post
                                        </label>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.title}">
                                    <label for="title">Title</label>
                                    <input type="text" class="form-control" :disabled="process_loader" id="title" name="title" v-model.trim="post.title" placeholder="Title">
                                    <small v-show="!process_loader" v-for="error in inputErrors.title" class="help-block">
                                        <strong>{{ error }}</strong>
                                    </small>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.content}">
                                    <label>Content</label>
                                    <small v-show="!process_loader" v-for="error in inputErrors.content" class="help-block">
                                        <strong>{{ error }}</strong>
                                    </small>
                                    <quill-editor ref="myTextEditor"
                                                  v-model="post.content">
                                    </quill-editor>
                                </div>

                                <div class="form-group">
                                    <button type="submit" class="btn btn-primary" :disabled="process_loader" data-toggle="modal" data-target="#createPost">Post</button>
                                </div>

                            </form>

                        </div><!-- end .panel-body -->
                    </div><!-- end .panel .panel-default -->

                </div><!-- end .col-md-9 -->
            </div><!-- end .row -->

        </div><!-- v-if="!fetchUserDataStatus.loading && !fetchUserDataStatus.error && permission.update_account" -->
        <top-progress ref="topProgress"></top-progress>

        <!-- Small modal -->
        <div class="modal fade" id="createPost" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>Create new post?</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="createpost_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import {mapState} from 'vuex'
import {quillEditor} from 'vue-quill-editor'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {apiDomain, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default {
  mounted () {
    this.$refs.topProgress.start()
  },

  created () {
    this.loadData()
  },
  components: {
    SideMenu,
    topProgress,
    quillEditor
  },

  data () {
    return {
      httpLoad: {
        process: true,
        httpCode: 0,
        access: false,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      post: {
        title: '',
        content: '',
        pinned: false,
        is_public: false
      },
      inputErrors: [],
      enable_panel_login: false
    }
  },

  head: {
    // To use "this" in the component, it is necessary to return the object through a function
    title: function () {
      return {
        inner: this.$store.state.siteOptionsStore.sub_name,
        separator: this.$store.state.siteOptionsStore.separator,
        complement: this.$store.state.siteOptionsStore.site_name
      }
    }
  },

  computed: {
    process_loader () {
      return this.httpRequest.process
    },

    process_trigger () {
      return !this.process_loader && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.process_loader && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      siteOptionsStore: state => state.siteOptionsStore,
      langStore: state => state.langStore
    })
  },

  methods: {
    loadData () {
      this.httpLoad.message = 'Accessing Page...'
      this.$http.get(apiDomain + 'news-and-updates-create', {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        this.$store.dispatch('setSiteName', response.data.site_options.site_name)
        this.$store.dispatch('setSubName', response.data.site_options.sub_name)
        this.$store.dispatch('setSeparator', '-')
        this.$emit('updateHead')

        this.$store.dispatch('setLanguage', response.data.language)
        this.$store.dispatch('setAccountProfile', response.data.profile)
        this.$store.dispatch('setAccountPermission', response.data.permission)

        var debounce = _.debounce(() => {
          this.$refs.topProgress.done()

          this.enable_panel_login = response.data.site_options.enable_panel_login

          this.httpLoad.httpCode = response.status
          this.httpLoad.access = true
          this.httpLoad.process = false
        }, 500)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        var sitename = 'VPN Panel'
        var subname = ' '
        var separator = ' '
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 403 && error.response.data.message) {
            errorMessage = error.response.data.message
            this.$store.dispatch('setAccountProfile', error.response.data.profile)
            this.$store.dispatch('setAccountPermission', error.response.data.permission)

            sitename = error.response.data.site_options.site_name
            subname = error.response.data.site_options.sub_name
            separator = '-'
          }
        }

        this.$store.dispatch('setSiteName', sitename)
        this.$store.dispatch('setSubName', subname)
        this.$store.dispatch('setSeparator', separator)
        this.$emit('updateHead')

        var debounce = _.debounce(() => {
          this.$refs.topProgress.fail()
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 1000)
        debounce()
      })
    },

    hideMsg () {
      this.httpRequest.trigger = false
      this.inputErrors = []
    },

    createpost_trigger () {
      if (!this.httpRequest.process) {
        this.httpRequest.message = 'Creating new post...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.create_post(), 500)
        debounce()
      }
    },

    create_post () {
      this.$http.post(apiDomain + 'news-and-updates-create', this.post, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        var debounce = _.debounce(() => {
          this.post.title = ''
          this.post.content = ''

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            errorMessage = 'Input Error.'
            this.inputErrors = error.response.data
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    }
  }
}
</script>



