<template>
    <div>
        <div v-if="!httpLoad.process && httpLoad.access && !enable_panel_login" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                Maintenance Mode.
            </div>
        </div>

        <div v-if="httpLoad.process" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="!httpLoad.process && !httpLoad.access" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="httpLoad.access">

            <div class="page-header">
                <h2>News and Updates</h2>
            </div>

            <div class="row">
                <side-menu></side-menu>
                <div class="col-md-9">

                    <div v-if="authUserStore.permission.is_admin" class="btn-group" role="group" aria-label="...">
                        <button :disabled="model.data && model.data.length === 0 || httpRequest.process" type="button" class="btn btn-danger" data-toggle="modal" data-target="#deletePost">Delete</button>
                        <button :disabled="model.data && model.data.length === 0 || httpRequest.process" type="button" class="btn btn-default" data-toggle="modal" data-target="#pinPost">Pin</button>
                        <button :disabled="model.data && model.data.length === 0 || httpRequest.process" type="button" class="btn btn-default" data-toggle="modal" data-target="#unPinPost">Un-Pin</button>
                        <div>&nbsp;</div>
                    </div>

                    <div v-if="httpRequest.process" class="loading">
                        <div class="alert alert-dismissible alert-info" role="alert">
                            <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                            {{ httpRequest.message }}
                        </div>
                    </div>

                    <div v-if="process_trigger && process_status.error" class="error">
                        <div class="alert alert-dismissible alert-danger" role="alert">
                            <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                            {{ httpRequest.message }}
                        </div>
                    </div>

                    <div v-if="process_trigger && !process_status.error" class="loading">
                        <div class="alert alert-dismissible alert-success" role="alert">
                            <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                            {{ httpRequest.message }}
                        </div>
                    </div>

                    <div class="list-group">
                        <div v-for="row in model.data" class="list-group-item">
                            <input v-if="authUserStore.permission.is_admin" type="checkbox" v-model="selected" :value="row.id" number>
                            <blockquote>
                                <router-link :to="{ name: 'post-item', params: { id: row.id }}" >
                                    <p>{{ row.title }}</p>
                                </router-link>
                                <footer>{{ row.created_at }}<template v-if="row.pinned"> #Pinned</template><template v-if="row.is_public"> #Public</template> <router-link v-if="authUserStore.permission.is_admin" :to="{ name: 'post-edit-item', params: { id: row.id }}" >Edit</router-link></footer>
                            </blockquote>
                        </div>
                        <template v-if="model.data && model.data.length === 0">
                            <blockquote>
                                <p>No post available</p>
                            </blockquote>
                        </template>
                    </div>

                </div><!-- end .col-md-9 -->
            </div><!-- end .row -->

        </div><!-- v-if="!fetchUserDataStatus.loading && !fetchUserDataStatus.error && permission.update_account" -->
        <top-progress ref="topProgress"></top-progress>

        <!-- Small modal -->
        <div class="modal fade" id="deletePost" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>Delete selected posts?</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="deletepost_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Small modal -->
        <div class="modal fade" id="pinPost" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel2">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>Pin selected posts?</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="pinpost_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Small modal -->
        <div class="modal fade" id="unPinPost" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel3">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>Un-Pin selected posts?</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="unpinpost_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {apiDomain, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default {
  mounted () {
    this.$refs.topProgress.start()
  },

  created () {
    this.$store.dispatch('setUserToken', this.$auth.getUserToken())
    this.loadData()
  },
  components: {
    SideMenu,
    topProgress
  },

  data () {
    return {
      httpLoad: {
        process: true,
        httpCode: 0,
        access: false,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      model: {},
      columns: {},
      selected: [],
      enable_panel_login: false
    }
  },

  head: {
    // To use "this" in the component, it is necessary to return the object through a function
    title: function () {
      return {
        inner: this.$store.state.siteOptionsStore.sub_name,
        separator: this.$store.state.siteOptionsStore.separator,
        complement: this.$store.state.siteOptionsStore.site_name
      }
    }
  },

  computed: {
    selectAll: {
      get: function () {
        return this.model.data ? this.selected.length === this.model.data.length : false
      },
      set: function (value) {
        var selected = []

        if (value) {
          this.model.data.forEach(function (user) {
            selected.push(user.id)
          })
        }

        this.selected = selected
      }
    },
    process_loader () {
      return this.httpRequest.process
    },

    process_trigger () {
      return !this.process_loader && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.process_loader && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      siteOptionsStore: state => state.siteOptionsStore,
      langStore: state => state.langStore
    })
  },

  methods: {
    loadData () {
      this.httpLoad.message = 'Accessing Page...'
      this.$http.get(apiDomain + 'news-and-updates', {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        this.$store.dispatch('setSiteName', response.data.site_options.site_name)
        this.$store.dispatch('setSubName', response.data.site_options.sub_name)
        this.$store.dispatch('setSeparator', '-')
        this.$emit('updateHead')

        this.$store.dispatch('setLanguage', response.data.language)
        this.$store.dispatch('setAccountProfile', response.data.profile)
        this.$store.dispatch('setAccountPermission', response.data.permission)

        var debounce = _.debounce(() => {
          this.$refs.topProgress.done()

          this.enable_panel_login = response.data.site_options.enable_panel_login

          this.model = response.data.model
          this.columns = response.data.columns

          this.httpLoad.httpCode = response.status
          this.httpLoad.access = true
          this.httpLoad.process = false
        }, 500)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        var sitename = 'VPN Panel'
        var subname = ' '
        var separator = ' '
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 403 && error.response.data.message) {
            errorMessage = error.response.data.message
            this.$store.dispatch('setAccountProfile', error.response.data.profile)
            this.$store.dispatch('setAccountPermission', error.response.data.permission)

            sitename = error.response.data.site_options.site_name
            subname = error.response.data.site_options.sub_name
            separator = '-'
          }
        }

        this.$store.dispatch('setSiteName', sitename)
        this.$store.dispatch('setSubName', subname)
        this.$store.dispatch('setSeparator', separator)
        this.$emit('updateHead')

        var debounce = _.debounce(() => {
          this.$refs.topProgress.fail()
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 1000)
        debounce()
      })
    },

    hideMsg () {
      this.httpRequest.trigger = false
    },

    deletepost_trigger () {
      if (!this.httpRequest.process) {
        this.httpRequest.message = 'Deleting selected posts...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        var debounce = _.debounce(() => this.delete_post(), 500)
        debounce()
      }
    },

    delete_post () {
      var deleteId = {
        id: this.selected
      }
      this.$http.post(apiDomain + 'news-and-updates/delete', deleteId, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        var debounce = _.debounce(() => {
          this.selected = []

          this.model = response.data.model
          this.columns = response.data.columns

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            errorMessage = 'Input Error.'
            this.inputErrors = error.response.data
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    pinpost_trigger () {
      if (!this.httpRequest.process) {
        this.httpRequest.message = 'Pin selected posts...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        var debounce = _.debounce(() => this.pin_post(), 500)
        debounce()
      }
    },

    pin_post () {
      var pinId = {
        id: this.selected
      }
      this.$http.post(apiDomain + 'news-and-updates/pin-post', pinId, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        var debounce = _.debounce(() => {
          this.selected = []

          this.model = response.data.model
          this.columns = response.data.columns

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            errorMessage = 'Input Error.'
            this.inputErrors = error.response.data
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    unpinpost_trigger () {
      if (!this.httpRequest.process) {
        this.httpRequest.message = 'Un-Pin selected posts...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        var debounce = _.debounce(() => this.unpin_post(), 500)
        debounce()
      }
    },

    unpin_post () {
      var pinId = {
        id: this.selected
      }
      this.$http.post(apiDomain + 'news-and-updates/unpin-post', pinId, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        var debounce = _.debounce(() => {
          this.selected = []

          this.model = response.data.model
          this.columns = response.data.columns

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            errorMessage = 'Input Error.'
            this.inputErrors = error.response.data
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    }
  }
}
</script>



