<template>
    <div>
        <div v-if="!httpLoad.process && httpLoad.access && !enable_panel_login" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                Maintenance Mode.
            </div>
        </div>

        <div v-if="httpLoad.process" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="!httpLoad.process && !httpLoad.access" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="httpLoad.access">

            <div class="page-header">
                <h2>{{ langStore.language[2] }}</h2>
            </div>

            <div class="row">

                <side-menu></side-menu>

                <div class="col-md-9">

                    <div class="panel panel-default">

                        <div class="panel-heading">
                            <h3 class="panel-title">{{ langStore.language[2] }}</h3>
                        </div>

                        <div class="panel-body">

                            <div v-if="httpRequest.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="success">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <form class="form-horizontal" v-on:submit.prevent="">

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Account Credits :</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static">
                                            <template v-if="authUserStore.profile.credits==='No Limit'">
                                                <span class="label label-success">{{ authUserStore.profile.credits }}</span>
                                            </template>
                                            <template v-else>
                                                <span :class="{'label ': true, 'label-primary': authUserStore.profile.credits > 0, 'label-danger': authUserStore.profile.credits == 0}">{{ authUserStore.profile.credits }}</span>
                                            </template>
                                        </p>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Expiration Date :</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static"><span :class="{'label ': true, 'label-default': authUserStore.profile.expired_at === 'Expired', 'label-success': authUserStore.profile.expired_at !== 'Expired'}">{{ authUserStore.profile.expired_at }}</span></p>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.credits}">
                                    <label for="credits" class="col-sm-3 control-label">Credits</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="credits" name="credits" v-model.trim="account.credits" placeholder="Credits">
                                        <small v-show="!process_loader" v-for="error in inputErrors.credits" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <div class="col-sm-offset-3 col-sm-9">
                                        <button type="submit" class="btn btn-primary" :disabled="process_loader" data-toggle="modal" data-target="#transferCredit">Extend</button> <button type="submit" class="btn btn-primary" :disabled="process_loader" v-on:click.prevent="loadDataUserVoucher">Voucher Code</button>
                                    </div>
                                </div>

                            </form>

                        </div><!-- end .panel-body -->

                    </div><!-- end .panel .panel-default -->

                </div><!-- end .col-md-9 -->

            </div><!-- end .row -->

        </div><!-- end v-if="!this.fetchUserDataStatus.loading && !this.fetchUserDataStatus.error && permission.update_account" -->
        <top-progress ref="topProgress"></top-progress>

        <!-- Small modal -->
        <div class="modal fade" id="transferCredit" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>
                            Extend duration using your credits?
                        </p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="extendduration_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

        <template v-if="modal[0].show">
            <transition name="modal">
                <div class="modal-mask">
                    <div class="modal-dialog" role="document">
                        <div class="modal-content">
                            <div class="modal-header">
                                <button type="button" class="close" v-on:click.prevent="modal[0].show = false"><span aria-hidden="true">×</span></button>
                                <h4 class="modal-title" id="ModalLabelUserVoucher">Apply Voucher Code</h4>
                            </div>
                            <div class="modal-body">

                                <div v-if="httpLoadUser.process" class="loading">
                                    <div class="alert alert-dismissible alert-info" role="alert">
                                        <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                        {{ httpLoadUser.message }}
                                    </div>
                                </div>

                                <div v-if="!httpLoadUser.process && !httpLoadUser.access" class="error">
                                    <div class="alert alert-dismissible alert-danger" role="alert">
                                        {{ httpLoadUser.message }}
                                    </div>
                                </div>

                                <template v-if="httpLoadUser.access">

                                    <div v-if="httpRequestUser.process" class="loading">
                                        <div class="alert alert-dismissible alert-info" role="alert">
                                            <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                            {{ httpRequestUser.message }}
                                        </div>
                                    </div>

                                    <div v-if="process_trigger_user && process_status_user.error" class="error">
                                        <div class="alert alert-dismissible alert-danger" role="alert">
                                            <button type="button" class="close" @click="hideMsgUser"><span>&times;</span></button>
                                            {{ httpRequestUser.message }}
                                        </div>
                                    </div>

                                    <div v-if="process_trigger_user && !process_status_user.error" class="success">
                                        <div class="alert alert-dismissible alert-success" role="alert">
                                            <button type="button" class="close" @click="hideMsgUser"><span>&times;</span></button>
                                            {{ httpRequestUser.message }}
                                        </div>
                                    </div>

                                    <form>

                                        <div class="form-group">
                                            <label class="control-label">Duration :</label>
                                            <span :class="{'label ': true, 'label-default': profile.expired_at === 'Expired', 'label-success': profile.expired_at !== 'Expired'}">{{ profile.expired_at }}</span>
                                        </div>

                                        <div :class="{'form-group' : true, 'has-error' : !httpRequestUser.process && inputErrorsUser.voucher_code}">
                                            <label for="voucher_code" class="control-label">Voucher Code:</label>
                                            <input type="text" id="voucher_code" class="form-control input-sm" :disabled="httpRequestUser.process" v-model="user_voucher.voucher_code" />
                                            <small v-if="!httpRequestUser.process" v-for="error in inputErrorsUser.voucher_code" class="help-block">
                                                <strong>{{ error }}</strong>
                                            </small>
                                        </div>

                                        <div class="form-group">
                                            <button type="submit" class="btn btn-primary btn-sm" :disabled="httpRequestUser.process" v-on:click.prevent="applyvoucher_trigger">Apply Voucher</button>
                                        </div>

                                    </form>

                                </template>

                            </div>

                        </div>
                    </div>
                </div>
            </transition>
        </template>

    </div>
</template>

<style>
</style>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {apiDomain, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default {
  mounted () {
    this.$refs.topProgress.start()
  },
  created () {
    // console.log('Account Password created1.')
    this.loadData()
  },

  components: {
    SideMenu,
    topProgress
  },

  computed: {
    process_loader () {
      return this.httpRequest.process
    },

    process_trigger () {
      return !this.process_loader && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.process_loader && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    process_loader_user () {
      return this.httpRequestUser.process
    },

    process_trigger_user () {
      return !this.process_loader_user && this.httpRequestUser.trigger
    },

    process_status_user () {
      return {
        error: !this.process_loader_user && this.httpRequestUser.httpCode !== 200,
        message: this.httpRequestUser.message
      }
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      siteOptionsStore: state => state.siteOptionsStore,
      langStore: state => state.langStore
    })
  },

  data () {
    return {
      httpLoad: {
        process: true,
        httpCode: 0,
        access: false,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      httpLoadUser: {
        process: true,
        httpCode: 0,
        access: false,
        message: null
      },
      httpRequestUser: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      modal: [
        { name: 'UserVoucher', show: false }
      ],
      inputErrors: [],
      inputErrorsUser: [],
      account: {
        credits: null
      },
      profile: {},
      user_voucher: {},
      enable_panel_login: false
    }
  },

  head: {
    // To use "this" in the component, it is necessary to return the object through a function
    title: function () {
      return {
        inner: this.$store.state.siteOptionsStore.sub_name,
        separator: this.$store.state.siteOptionsStore.separator,
        complement: this.$store.state.siteOptionsStore.site_name
      }
    }
  },

  methods: {
    loadData () {
      this.httpLoad.message = 'Accessing Page...'
      this.$http.get(apiDomain + 'account/extend-duration', {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        this.$store.dispatch('setSiteName', response.data.site_options.site_name)
        this.$store.dispatch('setSubName', response.data.site_options.sub_name)
        this.$store.dispatch('setSeparator', '-')
        this.$emit('updateHead')

        this.$store.dispatch('setLanguage', response.data.language)
        this.$store.dispatch('setAccountProfile', response.data.profile)
        this.$store.dispatch('setAccountPermission', response.data.permission)

        var debounce = _.debounce(() => {
          this.$refs.topProgress.done()

          this.enable_panel_login = response.data.site_options.enable_panel_login

          this.httpLoad.httpCode = response.status
          this.httpLoad.access = true
          this.httpLoad.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        var sitename = 'VPN Panel'
        var subname = ' '
        var separator = ' '
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 403 && error.response.data.message) {
            errorMessage = error.response.data.message
            this.$store.dispatch('setAccountProfile', error.response.data.profile)
            this.$store.dispatch('setAccountPermission', error.response.data.permission)

            sitename = error.response.data.site_options.site_name
            subname = error.response.data.site_options.sub_name
            separator = '-'
          }
        }

        this.$store.dispatch('setSiteName', sitename)
        this.$store.dispatch('setSubName', subname)
        this.$store.dispatch('setSeparator', separator)
        this.$emit('updateHead')

        var debounce = _.debounce(() => {
          this.$refs.topProgress.fail()
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 1000)
        debounce()
      })
    },

    hideMsg () {
      this.httpRequest.trigger = false
      this.inputErrors = []
    },

    extendduration_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Extending duration...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.extend_duration(), 500)
        debounce()
      }
    },

    extend_duration () {
      this.$http.post(apiDomain + 'account/extend-duration', this.account, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        // console.log(response)

        var debounce = _.debounce(() => {
          this.account = {}

          this.$store.dispatch('setAccountProfile', response.data.profile)

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            errorMessage = 'Input Error.'
            this.inputErrors = error.response.data
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    hideMsgUser () {
      this.httpRequestUser.trigger = false
      this.inputErrorsUser = []
    },

    loadDataUserVoucher () {
      this.inputErrorsUser = []
      this.httpLoadUser.process = true
      this.httpLoadUser.httpCode = 0
      this.httpLoadUser.access = false
      this.httpLoadUser.message = 'Loading user data...'

      this.httpRequestUser.process = false
      this.httpRequestUser.httpCode = 0
      this.httpRequestUser.trigger = null
      this.httpRequestUser.message = null

      this.modal[0].show = true

      this.user_voucher = {}

      this.$http.get(apiDomain + 'voucher/apply', {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        var debounce = _.debounce(() => {
          this.profile = response.data.profile
          this.httpLoadUser.httpCode = response.status
          this.httpLoadUser.access = true
          this.httpLoadUser.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 403 && error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpLoadUser.httpCode = errorHttpCode
          this.httpLoadUser.message = errorMessage
          this.httpLoadUser.process = false
        }, 1000)
        debounce()
      })
    },

    applyvoucher_trigger () {
      if (!this.process_loader) {
        this.httpRequestUser.message = 'Applying voucher...'
        this.httpRequestUser.trigger = true
        this.httpRequestUser.process = true
        this.inputErrorsUser = []
        var debounce = _.debounce(() => this.apply_voucher(), 500)
        debounce()
      }
    },

    apply_voucher () {
      this.$http.post(apiDomain + 'voucher/apply', this.user_voucher, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        // console.log(response)

        var debounce = _.debounce(() => {
          this.user_voucher = {}
          this.profile = response.data.profile

          this.httpRequestUser.httpCode = response.status
          this.httpRequestUser.message = response.data.message
          this.httpRequestUser.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            errorMessage = 'Input Error.'
            this.inputErrorsUser = error.response.data
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequestUser.httpCode = errorHttpCode
          this.httpRequestUser.message = errorMessage
          this.httpRequestUser.process = false
        }, 1000)
        debounce()
      })
    }
  }
}
</script>



