<template>
    <div>
        <div v-if="!httpLoad.process && httpLoad.access && !enable_panel_login" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                Maintenance Mode.
            </div>
        </div>

        <div v-if="httpLoad.process" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="!httpLoad.process && !httpLoad.access" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="httpLoad.access">

            <div class="page-header">
                <h2>Settings</h2>
            </div>

            <div class="row">
                <side-menu></side-menu>
                <div class="col-md-9">

                    <div class="panel panel-default">

                        <div class="panel-heading">
                            <h3 class="panel-title">Settings</h3>
                        </div><!-- end .panel-heading -->

                        <div class="panel-body">

                            <div v-if="httpRequest.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="success">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <form class="form-horizontal" v-on:submit.prevent="">

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.site_name}">
                                    <label for="site_name" class="col-sm-3 control-label">Site Name</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="site_name" name="site_name" v-model.trim="site_settings.site_name" placeholder="Site Name">
                                        <small v-show="!process_loader" v-for="error in inputErrors.site_name" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Enable DB Backup</label>
                                    <div class="col-sm-9">
                                        <div class="checkbox">
                                            <label>
                                                <input @click="db_cron_click" :disabled="process_loader" type="checkbox" v-model="site_settings.backup">
                                            </label>
                                        </div>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Enable Data Reset</label>
                                    <div class="col-sm-9">
                                        <div class="checkbox">
                                            <label>
                                                <input @click="datareset_cron_click" :disabled="process_loader" type="checkbox" v-model="site_settings.data_reset">
                                            </label>
                                        </div>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.db_backup_schedule}">
                                    <label for="db_backup_schedule" class="col-sm-3 control-label">DB Backup Schedule</label>
                                    <div class="col-sm-9">
                                        <div class="input-group">
                                            <input type="text" class="form-control" :disabled="process_loader || !site_settings.backup" id="db_backup_schedule" name="db_backup_schedule" v-model.trim="site_settings.db_backup_schedule" placeholder="Cron Format">
                                            <span class="input-group-addon">
                                                Cron Format
                                            </span>
                                        </div>
                                        <small v-show="!process_loader" v-for="error in inputErrors.db_backup_schedule" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.data_reset_schedule}">
                                    <label for="data_reset_schedule" class="col-sm-3 control-label">Data Reset Schedule</label>
                                    <div class="col-sm-9">
                                        <div class="input-group">
                                            <input type="text" class="form-control" :disabled="process_loader || !site_settings.data_reset" id="data_reset_schedule" name="data_reset_schedule" v-model.trim="site_settings.data_reset_schedule" placeholder="Cron Format">
                                            <span class="input-group-addon">
                                                Cron Format
                                            </span>
                                        </div>
                                        <small v-show="!process_loader" v-for="error in inputErrors.data_reset_schedule" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.data_allowance}">
                                    <label for="data_allowance" class="col-sm-3 control-label">Data Allowance</label>
                                    <div class="col-sm-9">
                                        <div class="input-group">
                                            <input type="text" class="form-control" :disabled="process_loader" id="data_allowance" name="data_allowance" v-model.trim="site_settings.data_allowance" placeholder="1GB = 1024MB">
                                            <span class="input-group-addon">
                                                in Megabyte
                                            </span>
                                        </div>
                                        <small v-show="!process_loader" v-for="error in inputErrors.data_allowance" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.trial_period}">
                                    <label for="trial_period" class="col-sm-3 control-label">Trial Period</label>
                                    <div class="col-sm-9">
                                        <div class="input-group">
                                            <input type="text" class="form-control" :disabled="process_loader" id="trial_period" name="trial_period" v-model.trim="site_settings.trial_period" placeholder="1 = 1 Hour">
                                            <span class="input-group-addon">
                                                in Hour(s)
                                            </span>
                                        </div>
                                        <small v-show="!process_loader" v-for="error in inputErrors.trial_period" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.max_transfer_credits}">
                                    <label for="max_transfer_credits" class="col-sm-3 control-label">Max Transfer Credits</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="max_transfer_credits" v-model.trim="site_settings.max_transfer_credits" placeholder="Max Transfer Credits">
                                        <small v-show="!process_loader" v-for="error in inputErrors.max_transfer_credits" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Enable Panel Login</label>
                                    <div class="col-sm-9">
                                        <div class="checkbox">
                                            <label>
                                                <input :disabled="process_loader" type="checkbox" v-model="site_settings.enable_panel_login">
                                            </label>
                                        </div>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Enable VPN Login</label>
                                    <div class="col-sm-9">
                                        <div class="checkbox">
                                            <label>
                                                <input :disabled="process_loader" type="checkbox" v-model="site_settings.enable_vpn_login">
                                            </label>
                                        </div>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Public Distributors</label>
                                    <div class="col-sm-9">
                                        <div class="checkbox">
                                            <label>
                                                <input :disabled="process_loader" type="checkbox" v-model="site_settings.public_credit_distributors">
                                            </label>
                                        </div>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Public Online users</label>
                                    <div class="col-sm-9">
                                        <div class="checkbox">
                                            <label>
                                                <input :disabled="process_loader" type="checkbox" v-model="site_settings.public_online_users">
                                            </label>
                                        </div>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Public Server Status</label>
                                    <div class="col-sm-9">
                                        <div class="checkbox">
                                            <label>
                                                <input :disabled="process_loader" type="checkbox" v-model="site_settings.public_server_status">
                                            </label>
                                        </div>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <div class="col-sm-offset-3 col-sm-9">
                                        <button type="submit" class="btn btn-primary" :disabled="process_loader" data-toggle="modal" data-target="#updateSettings">Save</button>
                                    </div>
                                </div>

                            </form><!-- end .form-horizontal -->

                        </div><!-- end .panel-body -->
                    </div><!-- end .panel .panel-default -->

                </div><!-- end .col-md-6 -->
            </div><!-- end .row -->
        </div><!-- end v-if="!fetchUserDataStatus.loading && !fetchUserDataStatus.error" -->
        <top-progress ref="topProgress"></top-progress>

        <!-- Small modal -->
        <div class="modal fade" id="updateSettings" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>Update settings?</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="updatesettings_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {apiDomain, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default{
  mounted () {
    this.$refs.topProgress.start()
  },
  created () {
    // console.log('Server Info created1.')
    this.loadData()
  },

  components: {
    SideMenu,
    topProgress
  },

  data () {
    return {
      httpLoad: {
        process: true,
        httpCode: 0,
        access: false,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      inputErrors: [],
      site_settings: {},
      static_cron_db: null,
      static_cron_datareset: null,
      enable_panel_login: false
    }
  },

  head: {
    // To use "this" in the component, it is necessary to return the object through a function
    title: function () {
      return {
        inner: this.$store.state.siteOptionsStore.sub_name,
        separator: this.$store.state.siteOptionsStore.separator,
        complement: this.$store.state.siteOptionsStore.site_name
      }
    }
  },

  computed: {
    process_loader () {
      return this.httpRequest.process
    },

    process_trigger () {
      return !this.process_loader && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.process_loader && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      siteOptionsStore: state => state.siteOptionsStore,
      langStore: state => state.langStore
    })
  },

  methods: {
    loadData () {
      this.httpLoad.message = 'Accessing Page...'
      this.$http.get(apiDomain + 'admin/site-settings', {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        // console.log(response)
        this.$store.dispatch('setSiteName', response.data.site_options.site_name)
        this.$store.dispatch('setSubName', response.data.site_options.sub_name)
        this.$store.dispatch('setSeparator', '-')
        this.$emit('updateHead')

        this.$store.dispatch('setLanguage', response.data.language)
        this.$store.dispatch('setAccountProfile', response.data.profile)
        this.$store.dispatch('setAccountPermission', response.data.permission)

        var debounce = _.debounce(() => {
          this.$refs.topProgress.done()

          this.enable_panel_login = response.data.site_options.enable_panel_login

          this.site_settings = response.data.site_settings
          this.static_cron_db = this.site_settings.db_backup_schedule
          this.static_cron_datareset = this.site_settings.data_reset_schedule

          this.httpLoad.httpCode = response.status
          this.httpLoad.access = true
          this.httpLoad.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        var sitename = 'VPN Panel'
        var subname = ' '
        var separator = ' '
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 403 && error.response.data.message) {
            errorMessage = error.response.data.message
            this.$store.dispatch('setAccountProfile', error.response.data.profile)
            this.$store.dispatch('setAccountPermission', error.response.data.permission)

            sitename = error.response.data.site_options.site_name
            subname = error.response.data.site_options.sub_name
            separator = '-'
            // this.enable_panel_login = error.response.data.site_options.enable_panel_login
          }
        }

        this.$store.dispatch('setSiteName', sitename)
        this.$store.dispatch('setSubName', subname)
        this.$store.dispatch('setSeparator', separator)
        this.$emit('updateHead')

        var debounce = _.debounce(() => {
          this.$refs.topProgress.fail()
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 1000)
        debounce()
      })
    },

    hideMsg () {
      this.httpRequest.trigger = false
      this.inputErrors = []
    },

    updatesettings_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Updating settings...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.update_settings(), 500)
        debounce()
      }
    },

    update_settings () {
      this.$http.post(apiDomain + 'admin/site-settings', this.site_settings, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        // console.log(response)

        var debounce = _.debounce(() => {
          this.$store.dispatch('setSiteName', this.site_settings.site_name)
          this.$emit('updateHead')

          this.enable_panel_login = this.site_settings.enable_panel_login

          this.static_cron_db = this.site_settings.db_backup_schedule
          this.static_cron_datareset = this.site_settings.data_reset_schedule

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            errorMessage = 'Input Error.'
            this.inputErrors = error.response.data
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    db_cron_click () {
      this.hideMsg()
      if (!this.site_settings.backup) {
        this.site_settings.db_backup_schedule = this.static_cron_db
      }
    },

    datareset_cron_click () {
      this.hideMsg()
      if (!this.site_settings.data_reset) {
        this.site_settings.data_reset_schedule = this.static_cron_datareset
      }
    }
  }
}
</script>



