import Vue from 'vue'
import VueRouter from 'vue-router'
import Url404 from './components/error_pages/Page404.vue'
// import HomePage from './components/HomePage.vue'
import LoginComponent from './components/Login.vue'
// import DashboardComponent from './components/Dashboard.vue'
import AccountProfileComponent from './components/account/Profile.vue'
import AccountPasswordComponent from './components/account/Password.vue'
import ExtendDurationComponent from './components/account/ExtendDuration.vue'
import TransferCreditsComponent from './components/account/TrasferCredits.vue'
import VacationModeComponent from './components/account/VacationMode.vue'
import AccountVpnComponent from './components/account/VpnStatus.vue'
import CreditTransferLogsComponent from './components/account/CreditTransferLogs.vue'

/* Voucher */
// import ApplyVoucherComponent from './components/voucher/ApplyVoucher.vue'
import GenerateVoucherComponent from './components/voucher/GenerateVoucher.vue'

/* Distributor */
import CreditDistributorComponent from './components/Distributor.vue'
import PublicCreditDistributorComponent from './components/PublicDistributor.vue'

/* News and Updates */
import CreateNewsAndUpdatesComponent from './components/news_updates/CreateNewsAndUpdates.vue'
import NewsAndUpdatesComponent from './components/news_updates/NewsAndUpdates.vue'
import NewsAndUpdatesItemComponent from './components/news_updates/NewsAndUpdatesItem.vue'
import NewsAndUpdatesEditItemComponent from './components/news_updates/NewsAndUpdatesEditItem.vue'
import PublicNewsAndUpdatesListComponent from './components/news_updates/PublicNewAndUpdatesList.vue'
import PublicNewsAndUpdatesItemComponent from './components/news_updates/PublicNewsAndUpdatesItem.vue'

/* Online Users */
import OnlineUsersComponent from './components/OnlineUsers.vue'
import PublicOnlineUsersComponent from './components/PublicOnlineUsers.vue'

// import ManageUsers from './components/ManageUsers.vue'
import ManageUserAll from './components/manage_user/UserListAll.vue'
import ManageUserUltimate from './components/manage_user/UserListUltimate.vue'
import ManageUserPremium from './components/manage_user/UserListPremium.vue'
import ManageUserReseller from './components/manage_user/UserListReseller.vue'
import ManageUserClient from './components/manage_user/UserListClient.vue'
// import UserProfileComponent from './components/manage_user/UserProfile.vue'
// import UserSecurityComponent from './components/manage_user/UserSecurity.vue'
// import UserPermissionComponent from './components/manage_user/UserPermission.vue'
// import UserDurationComponent from './components/manage_user/UserDuration.vue'
// import UserCreditsComponent from './components/manage_user/UserCredits.vue'
// import UserVoucherComponent from './components/manage_user/UserVoucher.vue'
import CreateUserComponent from './components/manage_user/UserCreate.vue'
import ManageUserTrashComponent from './components/manage_user/UserListTrash.vue'

/* Vpn Server */
import VpnServerListComponent from './components/vpn_server/ServerList.vue'
import VpnServerInfoComponent from './components/vpn_server/ServerInfo.vue'
import VpnServerAddComponent from './components/vpn_server/ServerCreate.vue'
import ServerStatusComponent from './components/ServerStatus.vue'
import PublicServerStatusComponent from './components/PublicServerStatus.vue'

/* Admin - Settings */
import AdminSettingsComponent from './components/admin/Settings.vue'
import AdminCreditTransferLogsComponent from './components/admin/CreditTransferLogs.vue'

Vue.use(VueRouter)

const rootDir = '/'

const router = new VueRouter({

  mode: 'history',
  routes: [
    {
      path: rootDir + 'auth/login',
      name: 'login',
      component: LoginComponent,
      beforeEnter: (to, from, next) => {
        if (!Vue.auth.isAuth()) {
          next()
        } else next({name: 'account-profile'})
      }
    },
    {
      path: rootDir + 'logout',
      name: 'logout',
      component: LoginComponent,
      beforeEnter: (to, from, next) => {
        if (!Vue.auth.isAuth()) {
          next()
        } else next({name: 'account-profile'})
      }
    },
    {
      path: rootDir + 'account/profile',
      name: 'account-profile',
      component: AccountProfileComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'account/vacation-mode',
      name: 'vacation-mode',
      component: VacationModeComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'account/vpn',
      name: 'account-vpn',
      component: AccountVpnComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'account/security',
      name: 'account-security',
      component: AccountPasswordComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'account/transfer-credits',
      name: 'transfer-credit',
      component: TransferCreditsComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'account/extend-duration',
      name: 'extend-duration',
      component: ExtendDurationComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'account/credit-transfer-logs',
      name: 'account-credit-transfer-logs',
      component: CreditTransferLogsComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'manage-users/all',
      name: 'manageuser-all',
      component: ManageUserAll,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'manage-users/ultimate',
      name: 'manageuser-ultimate',
      component: ManageUserUltimate,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'manage-users/premium',
      name: 'manageuser-premium',
      component: ManageUserPremium,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'manage-users/reseller',
      name: 'manageuser-reseller',
      component: ManageUserReseller,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'manage-users/client',
      name: 'manageuser-client',
      component: ManageUserClient,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    /* {
      path: rootDir + 'manage-users/profile/:id',
      name: 'user-profile',
      component: UserProfileComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'manage-users/security/:id',
      name: 'user-security',
      component: UserSecurityComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'manage-users/permission/:id',
      name: 'user-permission',
      component: UserPermissionComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'manage-users/duration/:id',
      name: 'user-duration',
      component: UserDurationComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'manage-users/credits/:id',
      name: 'user-credits',
      component: UserCreditsComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'manage-users/voucher/:id',
      name: 'user-voucher',
      component: UserVoucherComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    }, */
    {
      path: rootDir + 'manage-users/create',
      name: 'manageuser-create',
      component: CreateUserComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'voucher/generate',
      name: 'voucher-generate',
      component: GenerateVoucherComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    /* {
      path: rootDir + 'voucher/apply',
      name: 'voucher-apply',
      component: ApplyVoucherComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    }, */
    {
      path: rootDir + 'manage-users/trash',
      name: 'manageuser-trash',
      component: ManageUserTrashComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'vpn-servers/list',
      name: 'vpnserver-list',
      component: VpnServerListComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'vpn-servers/server-info/:id',
      name: 'vpnserver-info',
      component: VpnServerInfoComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'vpn-servers/add',
      name: 'vpnserver-add',
      component: VpnServerAddComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'server-status',
      name: 'server-status',
      component: ServerStatusComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'public-server-status'})
      }
    },
    {
      path: rootDir + 'public/server-status',
      name: 'public-server-status',
      component: PublicServerStatusComponent,
      beforeEnter: (to, from, next) => {
        if (!Vue.auth.isAuth()) {
          next()
        } else next({name: 'server-status'})
      }
    },
    {
      path: rootDir + 'distributors',
      name: 'credit-distributors',
      component: CreditDistributorComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'public-credit-distributors'})
      }
    },
    {
      path: rootDir + 'public/distributors',
      name: 'public-credit-distributors',
      component: PublicCreditDistributorComponent,
      beforeEnter: (to, from, next) => {
        if (!Vue.auth.isAuth()) {
          next()
        } else next({name: 'credit-distributors'})
      }
    },
    {
      path: rootDir + 'online-users',
      name: 'online-users',
      component: OnlineUsersComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'public-online-users'})
      }
    },
    {
      path: rootDir + 'public/online-users',
      name: 'public-online-users',
      component: PublicOnlineUsersComponent,
      beforeEnter: (to, from, next) => {
        if (!Vue.auth.isAuth()) {
          next()
        } else next({name: 'online-users'})
      }
    },
    {
      path: rootDir + 'admin/settings',
      name: 'admin-settings',
      component: AdminSettingsComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir,
      name: 'home',
      component: NewsAndUpdatesComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'public-home'})
      }
    },
    {
      path: rootDir + 'post/create',
      name: 'create-post',
      component: CreateNewsAndUpdatesComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'post/:id',
      name: 'post-item',
      component: NewsAndUpdatesItemComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'post/edit/:id',
      name: 'post-edit-item',
      component: NewsAndUpdatesEditItemComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: rootDir + 'public/updates',
      name: 'public-home',
      component: PublicNewsAndUpdatesListComponent,
      beforeEnter: (to, from, next) => {
        if (!Vue.auth.isAuth()) {
          next()
        } else next({name: 'home'})
      }
    },
    {
      path: rootDir + 'public/post/:id',
      name: 'public-post-item',
      component: PublicNewsAndUpdatesItemComponent,
      beforeEnter: (to, from, next) => {
        if (!Vue.auth.isAuth()) {
          next()
        } else next({name: 'home'})
      }
    },
    {
      path: rootDir + 'admin/credit-transfer-logs',
      name: 'admin-credit-transfer-logs',
      component: AdminCreditTransferLogsComponent,
      beforeEnter: (to, from, next) => {
        if (Vue.auth.isAuth()) {
          next()
        } else next({name: 'login'})
      }
    },
    {
      path: '*',
      component: Url404,
      beforeEnter: (to, from, next) => {
        next({name: 'home'})
      }
    }
  ]
})

export default router



