<template>
    <div>
        <div v-if="!httpLoad.process && httpLoad.access && !enable_panel_login" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                Maintenance Mode.
            </div>
        </div>

        <div v-if="httpLoad.process" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="!httpLoad.process && !httpLoad.access" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="httpLoad.access">

            <div class="page-header">
                <h2>{{ langStore.language[10] }}</h2>
            </div>

            <div class="row">
                <side-menu></side-menu>
                <div class="col-md-9">

                    <div class="row">
                        <div class="col-md-6 col-sm-6 col-xs-12">
                            <div class="info-box">
                                <span class="info-box-icon bg-green"><i class="glyphicon glyphicon-user"></i></span>

                                <div class="info-box-content">
                                    <span class="info-box-text">{{ langStore.language[11] }}</span>
                                    <span class="info-box-number">
                                        {{ total }}
                                    </span>
                                </div>
                                <!-- /.info-box-content -->
                            </div>
                            <!-- /.info-box -->
                        </div>
                        <div class="col-md-6 col-sm-6 col-xs-12">
                            <div class="info-box">
                                <span class="info-box-icon bg-aqua"><i class="glyphicon glyphicon-hourglass"></i></span>

                                <div class="info-box-content">
                                    <span class="info-box-text">{{ langStore.language[12] }}</span>
                                    <span class="info-box-number">
                                        {{ new_users }}
                                    </span>
                                </div>
                                <!-- /.info-box-content -->
                            </div>
                            <!-- /.info-box -->
                        </div>
                    </div>

                    <div class="panel panel-default">

                        <div class="panel-heading">
                            <h3 class="panel-title">{{ langStore.language[10] }}</h3>
                        </div><!-- end .panel-heading -->

                        <div class="panel-body">

                            <form class="form-inline" v-on:submit.prevent="">
                                <div class="form-group">
                                    <label>Search By</label>
                                    <select class="form-control" v-model="query.search_column" :disabled="httpRequest.process">
                                        <option v-for="(value, key) in search_col" :value="value.value">{{value.name}}</option>
                                    </select>
                                </div>
                                <div class="form-group">
                                    <select class="form-control" v-model="query.search_operator" :disabled="httpRequest.process">
                                        <option v-for="(value, key) in operators" :value="key">{{value}}</option>
                                    </select>
                                </div>

                                <div class="form-group">
                                    <input type="text" class="form-control"
                                           placeholder="Search"
                                           v-model="query.search_input"
                                           @keyup.enter="fetchInitSearch()" :disabled="httpRequest.process">
                                </div>

                                <div class="form-group">
                                    <div class="btn-group" role="group" aria-label="...">
                                        <button type="button" :disabled="httpRequest.process" @click="changeStatusDisplay(-1)" :class="{'btn' : true, 'btn-primary' : query.status_id === -1, 'btn-default' : query.status_id !== -1}">All</button>
                                        <button type="button" :disabled="model.data && model.data.length === 0 || httpRequest.process" @click="changeStatusDisplay(1)" :class="{'btn' : true, 'btn-primary' : query.status_id === 1, 'btn-default' : query.status_id !== 1}">Deactivated</button>
                                        <button type="button" :disabled="model.data && model.data.length === 0 || httpRequest.process" @click="changeStatusDisplay(2)" :class="{'btn' : true, 'btn-primary' : query.status_id === 2, 'btn-default' : query.status_id !== 2}">Activated</button>
                                        <button type="button" :disabled="model.data && model.data.length === 0 || httpRequest.process" @click="changeStatusDisplay(3)" :class="{'btn' : true, 'btn-primary' : query.status_id === 3, 'btn-default' : query.status_id !== 3}">Suspended</button>
                                    </div>
                                </div>

                            </form>

                            <div>&nbsp;</div>

                            <div class="btn-group" role="group" aria-label="...">
                                <button :disabled="model.data && model.data.length === 0 || httpRequest.process" type="button" class="btn btn-danger" data-toggle="modal" data-target="#deleteUser">Delete</button>
                                <div>&nbsp;</div>
                            </div>

                            <div v-if="httpRequest.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="loading">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div class="table-responsive">
                                <table class="table table-striped table-condensed table-hover">
                                    <thead>
                                    <tr>
                                        <th><input :disabled="model.data && model.data.length === 0 || httpRequest.process" type="checkbox" v-model="selectAll"></th>
                                        <th v-for="column in columns" @click="toggleOrder(column)">
                                            <span :class="{'label label-default' : column === query.column}">{{ column_transformer(column) }}</span>
                                            <span v-if="column === query.column">
                                                <i v-if="query.direction === 'desc'" class="fa fa-sort-amount-desc"></i>
                                                <i v-else class="fa fa-sort-amount-asc"></i>
                                            </span>
                                        </th>
                                        <th>Action</th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    <tr v-for="(row, index) in model.data">
                                        <td><input :disabled="model.data && model.data.length === 0 || httpRequest.process" type="checkbox" v-model="selected" :value="row.id" number></td>
                                        <td><a href="" v-on:click.prevent="loadDataUserAccount(row.id, index)" title="User Account">{{ row.username }}</a></td>
                                        <td><span :class="'label label-' + row.user_group.class">{{ row.user_group.name }}</span></td>
                                        <td><span :class="'label label-' + row.user_package.class">{{ row.user_package.name }}</span></td>
                                        <td><span :class="'label label-' + row.status.class">{{ row.status.name_get }}</span></td>
                                        <td><span :class="{'label ': true, 'label-primary': row.credits > 0, 'label-danger': row.credits == 0}">{{ row.credits }}</span></td>
                                        <td><span :class="{'label ': true, 'label-default': row.expired_at === 'Expired', 'label-success': row.expired_at !== 'Expired'}">{{ row.expired_at }}</span></td>
                                        <td>{{ row.created_at }}</td>
                                        <td>[<a href="" v-on:click.prevent="loadDataUserSecurity(row.id)" title="User Security"><i class="glyphicon glyphicon-lock"></i></a>] [<a href="" v-on:click.prevent="loadDataUserGroup(row.id, index)" title="User Group"><i class="fa fa-users"></i></a>] <template v-if="authUserStore.permission.access_duration">[<a href="" v-on:click.prevent="loadDataUserDuration(row.id, index)" title="User Duration"><i class="fa fa-clock-o"></i></a>]</template> [<a href="" v-on:click.prevent="loadDataUserPackage(row.id, index)" title="User Package"><i class="fa fa-dropbox"></i></a>] [<a href="" v-on:click.prevent="loadDataUserCredit(row.id, index)" title="User Credit and Duration"><i class="fa fa-bars"></i></a>] [<a href="" v-on:click.prevent="loadDataUserVoucher(row.id, index)" title="Voucher"><i class="fa fa-qrcode"></i></a>]</td>
                                    </tr>
                                    <tr v-if="model.data && model.data.length === 0">
                                        <td colspan="8">No results</td>
                                    </tr>
                                    </tbody>
                                    <tfoot>
                                    <tr>
                                        <td colspan="8">

                                            <form class="form-inline" v-on:submit.prevent="">
                                                <div class="form-group">
                                                    <span>Displaying {{model.from}} - {{model.to}} of {{model.total}} rows Current Page: {{ model.current_page }} | </span>
                                                </div>
                                                <div class="form-group">
                                                    <button :disabled="model.data && model.data.length === 0 || httpRequest.process" class="btn btn-default" @click="prev()">&laquo;</button>
                                                </div>
                                                <div class="form-group">
                                                    <button :disabled="model.data && model.data.length === 0 || httpRequest.process" class="btn btn-default" @click="next()">&raquo;</button>
                                                </div>
                                            </form>
                                        </td>
                                    </tr>
                                    </tfoot>
                                </table>

                            </div>

                        </div><!-- end .panel-body -->
                    </div><!-- end .panel .panel-default -->
                </div><!-- end .col-md-6 -->
            </div><!-- end .row -->

        </div><!-- end v-if="page_display" -->
        <top-progress ref="topProgress"></top-progress>

        <!-- Small modal -->
        <div class="modal fade" id="deleteUser" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>
                            Delete selected users?
                        </p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="delete_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- template for the modal component -->
        <template v-if="modal[0].show">
            <transition name="fade">
                <div class="modal-mask">
                    <div class="modal-dialog" role="document">
                        <div class="modal-content">
                            <div class="modal-header">
                                <button type="button" class="close" v-on:click.prevent="modal[0].show = false" aria-label="Close"><span aria-hidden="true">×</span></button>
                                <h5 class="modal-title" id="ModalLabelEditUserAccount">User Account - {{ username_static }}</h5>
                            </div>
                            <div class="modal-body">

                                <div v-if="httpLoadUser.process" class="loading">
                                    <div class="alert alert-dismissible alert-info" role="alert">
                                        <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                        {{ httpLoadUser.message }}
                                    </div>
                                </div>

                                <div v-if="!httpLoadUser.process && !httpLoadUser.access" class="error">
                                    <div class="alert alert-dismissible alert-danger" role="alert">
                                        {{ httpLoadUser.message }}
                                    </div>
                                </div>

                                <template v-if="httpLoadUser.access">

                                    <div v-if="httpRequestUser.process" class="loading">
                                        <div class="alert alert-dismissible alert-info" role="alert">
                                            <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                            {{ httpRequestUser.message }}
                                        </div>
                                    </div>

                                    <div v-if="process_trigger_user && process_status_user.error" class="error">
                                        <div class="alert alert-dismissible alert-danger" role="alert">
                                            <button type="button" class="close" @click="hideMsgUser"><span>&times;</span></button>
                                            {{ httpRequestUser.message }}
                                        </div>
                                    </div>

                                    <div v-if="process_trigger_user && !process_status_user.error" class="success">
                                        <div class="alert alert-dismissible alert-success" role="alert">
                                            <button type="button" class="close" @click="hideMsgUser"><span>&times;</span></button>
                                            {{ httpRequestUser.message }}
                                        </div>
                                    </div>

                                    <template v-if="update_username">
                                        <div :class="{'form-group' : true, 'has-error' : !httpRequestUser.process && inputErrorsUser.username}">
                                            <label for="username" class="control-label">Username:</label>
                                            <input type="text" id="username" class="form-control input-sm" :disabled="httpRequestUser.process" v-model="user_account.username" />
                                            <small v-if="!httpRequestUser.process" v-for="error in inputErrorsUser.username" class="help-block">
                                                <strong>{{ error }}</strong>
                                            </small>
                                        </div>
                                    </template>

                                    <div :class="{'form-group' : true, 'has-error' : !httpRequestUser.process && inputErrorsUser.email}">
                                        <label for="email" class="control-label">Email:</label>
                                        <input type="text" id="email" class="form-control input-sm" :disabled="httpRequestUser.process" v-model="user_account.email" />
                                        <small v-if="!httpRequestUser.process" v-for="error in inputErrorsUser.email" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>

                                    <div :class="{'form-group' : true, 'has-error' : !httpRequestUser.process && inputErrorsUser.fullname}">
                                        <label for="fullname" class="control-label">Fullname:</label>
                                        <input type="text" id="fullname" class="form-control input-sm" :disabled="httpRequestUser.process" v-model="user_account.fullname" />
                                        <small v-if="!httpRequestUser.process" v-for="error in inputErrorsUser.fullname" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>

                                    <div v-if="user_account.user_group_id < 5" :class="{'form-group' : true, 'has-error' : !httpRequestUser.process && inputErrorsUser.max_users}">
                                        <label for="max_users" class="control-label">Max Users:</label>
                                        <input type="number" id="max_users" class="form-control input-sm" :disabled="httpRequestUser.process" v-model="user_account.max_users" />
                                        <small v-if="!httpRequestUser.process" v-for="error in inputErrorsUser.max_users" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>

                                    <div :class="{'form-group' : true, 'has-error' : !httpRequestUser.process && inputErrorsUser.status_id}">
                                        <label for="user_status" class="control-label">Status:</label>
                                        <select class="form-control" :disabled="process_loader_user" id="user_status" v-model="user_account.status_id">
                                            <template v-for='(option, index) in user_status_list'>
                                                <option :value="option.id">{{ option.name_set }}</option>
                                            </template>
                                        </select>
                                        <small v-if="!httpRequestUser.process" v-for="error in inputErrorsUser.status_id" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>

                                    <div class="form-group">
                                        <button type="submit" class="btn btn-primary btn-sm" :disabled="httpRequestUser.process" v-on:click="edituseraccount_trigger">Edit</button>
                                    </div>

                                </template>

                            </div>

                        </div>
                    </div>
                </div>
            </transition>
        </template>

        <template v-if="modal[1].show">
            <transition name="modal">
                <div class="modal-mask">
                    <div class="modal-dialog" role="document">
                        <div class="modal-content">
                            <div class="modal-header">
                                <button type="button" class="close" v-on:click.prevent="modal[1].show = false"><span aria-hidden="true">×</span></button>
                                <h4 class="modal-title" id="ModalLabelUserSecurity">User Security - {{ username_static }}</h4>
                            </div>
                            <div class="modal-body">

                                <div v-if="httpLoadUser.process" class="loading">
                                    <div class="alert alert-dismissible alert-info" role="alert">
                                        <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                        {{ httpLoadUser.message }}
                                    </div>
                                </div>

                                <div v-if="!httpLoadUser.process && !httpLoadUser.access" class="error">
                                    <div class="alert alert-dismissible alert-danger" role="alert">
                                        {{ httpLoadUser.message }}
                                    </div>
                                </div>

                                <template v-if="httpLoadUser.access">

                                    <div v-if="httpRequestUser.process" class="loading">
                                        <div class="alert alert-dismissible alert-info" role="alert">
                                            <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                            {{ httpRequestUser.message }}
                                        </div>
                                    </div>

                                    <div v-if="process_trigger_user && process_status_user.error" class="error">
                                        <div class="alert alert-dismissible alert-danger" role="alert">
                                            <button type="button" class="close" @click="hideMsgUser"><span>&times;</span></button>
                                            {{ httpRequestUser.message }}
                                        </div>
                                    </div>

                                    <div v-if="process_trigger_user && !process_status_user.error" class="success">
                                        <div class="alert alert-dismissible alert-success" role="alert">
                                            <button type="button" class="close" @click="hideMsgUser"><span>&times;</span></button>
                                            {{ httpRequestUser.message }}
                                        </div>
                                    </div>

                                    <form>

                                        <div :class="{'form-group' : true, 'has-error' : !httpRequestUser.process && inputErrorsUser.new_password}">
                                            <label for="password" class="control-label">New Password:</label>
                                            <input type="password" id="password" class="form-control input-sm" :disabled="httpRequestUser.process" v-model="user_password.new_password" />
                                            <small v-if="!httpRequestUser.process" v-for="error in inputErrorsUser.new_password" class="help-block">
                                                <strong>{{ error }}</strong>
                                            </small>
                                        </div>

                                        <div :class="{'form-group' : true, 'has-error' : !httpRequestUser.process && inputErrorsUser.new_password_confirmation}">
                                            <label for="new_password_confirmation" class="control-label">Confirm New Password:</label>
                                            <input type="password" id="new_password_confirmation" class="form-control input-sm" :disabled="httpRequestUser.process" v-model="user_password.new_password_confirmation" />
                                            <small v-if="!httpRequestUser.process" v-for="error in inputErrorsUser.new_password_confirmation" class="help-block">
                                                <strong>{{ error }}</strong>
                                            </small>
                                        </div>

                                        <div class="form-group">
                                            <button type="submit" class="btn btn-primary btn-sm" :disabled="httpRequestUser.process" v-on:click="editusersecurity_trigger">Edit</button>
                                        </div>

                                    </form>

                                </template>

                            </div>

                        </div>
                    </div>
                </div>
            </transition>
        </template>

        <template v-if="modal[2].show">
            <transition name="modal">
                <div class="modal-mask">
                    <div class="modal-dialog" role="document">
                        <div class="modal-content">
                            <div class="modal-header">
                                <button type="button" class="close" v-on:click.prevent="modal[2].show = false"><span aria-hidden="true">×</span></button>
                                <h4 class="modal-title" id="ModalLabelUserCredit">User Credit - {{ username_static }}</h4>
                            </div>
                            <div class="modal-body">

                                <div v-if="httpLoadUser.process" class="loading">
                                    <div class="alert alert-dismissible alert-info" role="alert">
                                        <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                        {{ httpLoadUser.message }}
                                    </div>
                                </div>

                                <div v-if="!httpLoadUser.process && !httpLoadUser.access" class="error">
                                    <div class="alert alert-dismissible alert-danger" role="alert">
                                        {{ httpLoadUser.message }}
                                    </div>
                                </div>

                                <template v-if="httpLoadUser.access">

                                    <div v-if="httpRequestUser.process" class="loading">
                                        <div class="alert alert-dismissible alert-info" role="alert">
                                            <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                            {{ httpRequestUser.message }}
                                        </div>
                                    </div>

                                    <div v-if="process_trigger_user && process_status_user.error" class="error">
                                        <div class="alert alert-dismissible alert-danger" role="alert">
                                            <button type="button" class="close" @click="hideMsgUser"><span>&times;</span></button>
                                            {{ httpRequestUser.message }}
                                        </div>
                                    </div>

                                    <div v-if="process_trigger_user && !process_status_user.error" class="success">
                                        <div class="alert alert-dismissible alert-success" role="alert">
                                            <button type="button" class="close" @click="hideMsgUser"><span>&times;</span></button>
                                            {{ httpRequestUser.message }}
                                        </div>
                                    </div>

                                    <div class="row">
                                        <div class="col-sm-6">
                                            <div class="form-group">
                                                <label class="control-label">User Credits :</label>
                                                <span :class="{'label ': true, 'label-primary': user_profile.credits > 0, 'label-success': user_profile.credits == 'No Limit', 'label-danger': user_profile.credits == 0}">{{ user_profile.credits }}</span>
                                            </div>
                                            <div class="form-group">
                                                <label class="control-label">User Duration :</label>
                                                <span :class="{'label ': true, 'label-default': user_profile.expired_at === 'Expired', 'label-success': user_profile.expired_at !== 'Expired'}">{{ user_profile.expired_at }}</span>
                                            </div>
                                        </div>
                                        <div class="col-sm-6">
                                            <div class="form-group">
                                                <label class="control-label">Account Credits :</label>
                                                <template v-if="profile_account.credits==='No Limit'">
                                                    <span class="label label-success">{{ profile_account.credits }}</span>
                                                </template>
                                                <template v-else>
                                                    <span :class="{'label ': true, 'label-primary': profile_account.credits > 0, 'label-danger': profile_account.credits == 0}">{{ profile_account.credits }}</span>
                                                </template>
                                            </div>
                                        </div>
                                    </div>

                                    <div :class="{'form-group' : true, 'has-error' : !httpRequestUser.process && inputErrorsUser.input_credits}">
                                        <label for="input_credits" class="control-label">Transfer / Top-Up Credits:</label>
                                        <input type="number" id="input_credits" class="form-control input-sm" :disabled="httpRequestUser.process" v-model="user_credit.input_credits" />
                                        <small v-if="!httpRequestUser.process" v-for="error in inputErrorsUser.input_credits" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>

                                    <div class="form-group">
                                        <button type="submit" class="btn btn-primary btn-sm" :disabled="httpRequestUser.process" v-on:click.prevent="editusercredit_trigger">Transfer / Top-up</button> <input type="checkbox" v-model="user_credit.top_up" :disabled="process_loader_user"> Top-Up
                                    </div>

                                </template>

                            </div>

                        </div>
                    </div>
                </div>
            </transition>
        </template>

