                                    </div>

                                    <div :class="{'form-group' : true, 'has-error' : !httpRequestUser.process && inputErrorsUser.input_hours}">
                                        <label for="duration_hours" class="control-label">Hours:</label>
                                        <input type="number" id="duration_hours" class="form-control input-sm" :disabled="httpRequestUser.process" v-model="user_duration.input_hours" />
                                        <small v-if="!httpRequestUser.process" v-for="error in inputErrorsUser.input_hours" class="help-block">
                                            <strong>{{ error }}</strong>
