</template>

<style>
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .1s ease;
}
.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
  transition: opacity 1s;
}
</style>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {apiDomain, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default{
  mounted () {
    this.$refs.topProgress.start()
  },
  created () {
    this.loadData()
  },

  components: {
    SideMenu,
    topProgress
  },

  data () {
    return {
      httpLoad: {
        process: true,
        httpCode: 0,
        access: false,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 200,
        trigger: null,
        message: null
      },
      httpLoadUser: {
        process: true,
        httpCode: 0,
        access: false,
        message: null
      },
      httpRequestUser: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      model: {},
      columns: {},
      total: 0,
      new_users: 0,
      query: {
        page: 1,
        column: 'created_at',
        direction: 'desc',
        per_page: 25,
        search_column: 'username',
        search_operator: 'like',
        search_input: '',
        status_id: -1
      },
      operators: {
        like: 'LIKE',
        equal: '=',
        not_equal: '<>',
        in: 'IN'
      },
      selected: [],
      search_col: [
        {value: 'username', name: 'Username'},
        {value: 'email', name: 'Email'}
      ],
      enable_panel_login: false,
      modal: [
        { name: 'UserAccount', show: false },
        { name: 'UserSecurity', show: false },
        { name: 'UserCredit', show: false },
        { name: 'UserVoucher', show: false },
        { name: 'UserGroup', show: false },
        { name: 'UserPackage', show: false },
        { name: 'UserDuration', show: false }
      ],
      profile_account: {},
      user_id: 0,
      user_index: -1,
      update_username: false,
      user_profile: {},
      user_account: {},
      user_password: {},
      user_credit: {},
      user_voucher: {},
      user_group: {},
      user_package: {},
      user_duration: {},
      user_group_list: [],
      user_status_list: [],
      user_package_list: [],
      username_static: null,
      inputErrorsUser: []
    }
  },

  head: {
    // To use "this" in the component, it is necessary to return the object through a function
    title: function () {
      return {
        inner: this.$store.state.siteOptionsStore.sub_name,
        separator: this.$store.state.siteOptionsStore.separator,
        complement: this.$store.state.siteOptionsStore.site_name
      }
    }
  },

  computed: {
    selectAll: {
      get: function () {
        return this.model.data ? this.selected.length === this.model.data.length : false
      },
      set: function (value) {
        var selected = []

        if (value) {
          this.model.data.forEach(function (user) {
            selected.push(user.id)
          })
        }

        this.selected = selected
      }
    },

    process_trigger () {
      return !this.httpRequest.process && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.httpRequest.process && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    process_loader_user () {
      return this.httpRequestUser.process
    },

    process_trigger_user () {
      return !this.process_loader_user && this.httpRequestUser.trigger
    },

    process_status_user () {
      return {
        error: !this.process_loader_user && this.httpRequestUser.httpCode !== 200,
        message: this.httpRequestUser.message
      }
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      siteOptionsStore: state => state.siteOptionsStore,
      langStore: state => state.langStore
    })
  },

  methods: {
    loadData () {
      this.httpLoad.message = 'Accessing Page...'
      this.$http.get(apiDomain + `manage-user/client?column=${this.query.column}&direction=${this.query.direction}&page=${this.query.page}&per_page=${this.query.per_page}&search_column=${this.query.search_column}&search_operator=${this.query.search_operator}&search_input=${this.query.search_input}&status_id=${this.query.status_id}`, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        this.$store.dispatch('setSiteName', response.data.site_options.site_name)
        this.$store.dispatch('setSubName', response.data.site_options.sub_name)
        this.$store.dispatch('setSeparator', '-')
        this.$emit('updateHead')

        this.$store.dispatch('setLanguage', response.data.language)
        this.$store.dispatch('setAccountProfile', response.data.profile)
        this.$store.dispatch('setAccountPermission', response.data.permission)

        var debounce = _.debounce(() => {
          this.$refs.topProgress.done()

          this.enable_panel_login = response.data.site_options.enable_panel_login

          this.model = response.data.model
          this.columns = response.data.columns
          this.total = response.data.total
          this.new_users = response.data.new_users

          this.httpLoad.httpCode = response.status
          this.httpLoad.access = true
          this.httpLoad.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        var sitename = 'VPN Panel'
        var subname = ' '
        var separator = ' '
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 403 && error.response.data.message) {
            errorMessage = error.response.data.message
            this.$store.dispatch('setLanguage', error.response.data.language)
            this.$store.dispatch('setAccountProfile', error.response.data.profile)
            this.$store.dispatch('setAccountPermission', error.response.data.permission)

            sitename = error.response.data.site_options.site_name
            subname = error.response.data.site_options.sub_name
            separator = '-'
          }
        }

        this.$store.dispatch('setSiteName', sitename)
        this.$store.dispatch('setSubName', subname)
        this.$store.dispatch('setSeparator', separator)
        this.$emit('updateHead')

        var debounce = _.debounce(() => {
          this.$refs.topProgress.fail()
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 1000)
        debounce()
      })
    },

    changeStatusDisplay (statusId) {
      this.query.status_id = statusId
      this.fetchInitSearch()
    },

    column_transformer (col) {
      if (col === 'username') {
        return 'Username'
      }
      if (col === 'user_group_id') {
        return 'User Group'
      }
      if (col === 'user_package_id') {
        return 'Package'
      }
      if (col === 'status_id') {
        return 'Status'
      }
      if (col === 'credits') {
        return 'Credits'
      }
      if (col === 'expired_at') {
        return 'Duration'
      }
      if (col === 'created_at') {
        return 'Joined Date'
      }
    },

    next () {
      if (this.model.next_page_url) {
        this.query.page++
        this.fetchSearchData()
      }
    },
    prev () {
      if (this.model.prev_page_url) {
        this.query.page--
        this.fetchSearchData()
      }
    },
    toggleOrder (column) {
      if (!this.httpRequest.process) {
        this.httpRequest.message = 'Reordering...'
        if (column === this.query.column) {
          // only change direction
          if (this.query.direction === 'desc') {
            this.query.direction = 'asc'
          } else {
            this.query.direction = 'desc'
          }
        } else {
          this.query.column = column
          this.query.direction = 'asc'
        }
        this.fetchSearchData()
      }
    },
    fetchInitSearch () {
      this.query.page = 1
      this.fetchSearchData()
    },

    fetchSearchData () {
      this.selected = []
      if (!this.httpRequest.process) {
        this.httpRequest.message = 'Fetching data...'
        this.httpRequest.process = true
        this.httpRequest.trigger = true
        this.$http.get(apiDomain + `manage-user/client?column=${this.query.column}&direction=${this.query.direction}&page=${this.query.page}&per_page=${this.query.per_page}&search_column=${this.query.search_column}&search_operator=${this.query.search_operator}&search_input=${this.query.search_input}&status_id=${this.query.status_id}`, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
          // console.log(response.data.model)
          var debounce = _.debounce(() => {
            this.model = response.data.model
            this.columns = response.data.columns

            this.httpRequest.httpCode = response.status
            this.httpRequest.process = false
            this.httpRequest.trigger = false
          }, 1000)
          debounce()
        }).catch(error => {
          // console.log(error.response)
          var errorMessage = 'Server Error.'
          var errorHttpCode = 0
          if (error.response) {
            errorHttpCode = error.response.status
            if (error.response.status === 401) {
              this.$auth.destroyUser()
              this.$store.dispatch('clearAuthUser')
              this.$router.push({name: 'login'})
            }
            if (error.response.data.message) {
              errorMessage = error.response.data.message
            }
          }
          var debounce = _.debounce(() => {
            this.httpRequest.httpCode = errorHttpCode
            this.httpRequest.message = errorMessage
            this.httpRequest.process = false
          }, 1000)
          debounce()
        })
      }
    },
    hideMsg () {
      this.httpRequest.trigger = false
    },

    delete_trigger () {
      if (!this.httpRequest.process) {
        this.httpRequest.message = 'Deleting selected users...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.userDelete(), 500)
        debounce()
      }
    },

    userDelete () {
      var deleteId = {
        id: this.selected,
        column: this.query.column,
        direction: this.query.direction,
        page: this.query.page,
        per_page: this.query.per_page,
        search_column: this.query.search_column,
        search_operator: this.query.search_operator,
        search_input: this.query.search_input,
        status_id: this.query.status_id
      }
      this.$http.post(apiDomain + 'manage-user/delete-client', deleteId, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        // console.log(response)

        var debounce = _.debounce(() => {
          this.selected = []

          this.model = response.data.model
          this.columns = response.data.columns
          this.total = response.data.total
          this.new_users = response.data.new_users

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    hideMsgUser () {
      this.httpRequestUser.trigger = false
      this.inputErrorsUser = []
    },

    loadDataUserAccount (userid, index) {
      this.username_static = '...'
      this.inputErrorsUser = []
      this.httpLoadUser.process = true
      this.httpLoadUser.httpCode = 0
      this.httpLoadUser.access = false
      this.httpLoadUser.message = 'Loading user data...'

      this.httpRequestUser.process = false
      this.httpRequestUser.httpCode = 0
      this.httpRequestUser.trigger = null
      this.httpRequestUser.message = null

      this.modal[0].show = true

      this.$http.get(apiDomain + 'manage-user/profile/' + userid, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        var debounce = _.debounce(() => {
          this.user_id = userid
          this.user_index = index

          this.update_username = response.data.update_username

          this.user_account = response.data.user_profile
          this.username_static = this.user_account.username
