<template>
<div>
    <div v-if="!httpLoad.process && httpLoad.access && !enable_panel_login" class="error">
        <div class="alert alert-dismissible alert-danger" role="alert">
            Maintenance Mode.
        </div>
    </div>

    <div v-if="httpLoad.process" class="loading">
        <div class="alert alert-dismissible alert-info" role="alert">
            <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
            {{ httpLoad.message }}
        </div>
    </div>

    <div v-if="!httpLoad.process && !httpLoad.access" class="error">
        <div class="alert alert-dismissible alert-danger" role="alert">
            {{ httpLoad.message }}
        </div>
    </div>

    <div v-if="httpLoad.access" class="container">

        <form class="form-signin" v-on:submit.prevent="login_trigger">

            <div class="panel panel-default">

                <div class="panel-heading">
                    <h3 class="panel-title">Please sign in</h3>
                </div>

                <div class="panel-body">

                    <div v-if="httpRequest.process" class="loading">
                        <div class="alert alert-dismissible alert-info" role="alert">
                            <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                            Logging you in...
                        </div>
                    </div>

                    <div v-if="!httpRequest.process && httpRequest.trigger && httpRequest.httpCode !== 200" class="error">
                        <div class="alert alert-dismissible alert-danger" role="alert">
                            <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                            {{ httpRequest.message }}
                        </div>
                    </div>

                    <div v-if="!httpRequest.process && httpRequest.trigger && httpRequest.httpCode === 200" class="success">
                        <div class="alert alert-success" role="alert">
                            Login successfully.
                        </div>
                    </div>

                    <div class="form-group">
                        <label for="username">Username</label>
                        <input :disabled="httpRequest.process" v-model="username" type="text" id="username" class="form-control" placeholder="Username" required autofocus>
                    </div>
                    <div class="form-group">
                        <label for="password">Password</label>
                        <input :disabled="httpRequest.process" v-model="password" type="password" id="password" class="form-control" placeholder="Password" required>
                    </div>

                    <button :disabled="httpRequest.process" class="btn btn-lg btn-primary btn-block" type="submit">Sign in</button>

                    <span class="label label-default">Developed by: mp3sniff v3.0</span>

                </div><!-- end .panel-body -->

            </div><!-- end .panel .panel-default -->



        </form>

    </div> <!-- /container -->
    <top-progress ref="topProgress"></top-progress>


</div>
</template>

<style>
.form-signin {
  padding-top: 40px;
  padding-bottom: 40px;
  max-width: 420px;
  padding: 15px;
  margin: 0 auto;
}
.form-signin .form-signin-heading,
.form-signin .checkbox {
  margin-bottom: 10px;
}
.form-signin .checkbox {
  font-weight: normal;
}
.form-signin .form-control {
  position: relative;
  height: auto;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="text"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 5px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
</style>

<script>
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import moment from 'moment'
import {loginUrl, apiDomain, getHeader} from './../config.js'
export default{
  mounted () {
    this.$refs.topProgress.start()
  },

  created () {
    this.$store.dispatch('clearAuthUser')
    this.loadData()
  },

  components: {
    topProgress
  },

  data () {
    return {
      httpLoad: {
        process: true,
        httpCode: 0,
        access: false,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      client_secret: null,
      username: '',
      password: '',
      enable_panel_login: false,
      public_online_users: false,
      public_credit_distributors: false,
      public_server_status: false
    }
  },

  head: {
    // To use "this" in the component, it is necessary to return the object through a function
    title: function () {
      return {
        inner: this.$store.state.siteOptionsStore.sub_name,
        separator: this.$store.state.siteOptionsStore.separator,
        complement: this.$store.state.siteOptionsStore.site_name
      }
    }
  },

  methods: {
    loadData () {
      this.httpLoad.message = 'Accessing Page...'
      this.$http.get(apiDomain + 'login_init').then(response => {
        this.$store.dispatch('setSiteName', response.data.site_options.site_name)
        this.$store.dispatch('setSubName', response.data.site_options.sub_name)
        this.$store.dispatch('setSeparator', '-')
        this.$emit('updateHead')

        this.$store.dispatch('setLanguage', response.data.language)
        this.$store.dispatch('setPublicOnlineUsers', response.data.site_options.public_online_users)
        this.$store.dispatch('setPublicCreditDistributors', response.data.site_options.public_credit_distributors)
        this.$store.dispatch('setPublicServerStatus', response.data.site_options.public_server_status)

        var debounce = _.debounce(() => {
          this.$refs.topProgress.done()

          this.enable_panel_login = response.data.site_options.enable_panel_login

          this.client_secret = response.data.client_secret

          this.httpLoad.httpCode = response.status
          this.httpLoad.access = true
          this.httpLoad.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        var sitename = 'VPN Panel'
        var subname = ' '
        var separator = ' '
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 403 && error.response.data.message) {
            errorMessage = error.response.data.message
            sitename = error.response.data.site_options.site_name
            subname = error.response.data.site_options.sub_name
            separator = '-'
            this.public_online_users = error.response.data.site_options.public_online_users
            this.public_credit_distributors = error.response.data.site_options.public_credit_distributors
            this.public_server_status = error.response.data.site_options.public_server_status
          }
        }

        this.$store.dispatch('setSiteName', sitename)
        this.$store.dispatch('setSubName', subname)
        this.$store.dispatch('setSeparator', separator)
        this.$emit('updateHead')

        this.$store.dispatch('setPublicOnlineUsers', this.public_online_users)
        this.$store.dispatch('setPublicCreditDistributors', this.public_credit_distributors)
        this.$store.dispatch('setPublicServerStatus', this.public_server_status)

        var debounce = _.debounce(() => {
          this.$refs.topProgress.fail()
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 1000)
        debounce()
      })
    },

    hideMsg () {
      this.httpRequest.trigger = false
    },
    login_trigger () {
      if (this.httpRequest.process === false) {
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        var debounceLogin = _.debounce(() => this.login(), 500)
        debounceLogin()
      }
    },

    login () {
      var loginData = {
        client_id: 2,
        client_secret: this.client_secret,
        grant_type: 'password',
        username: this.username,
        password: this.password
      }

      this.$http.post(loginUrl, loginData).then(response => {
        const userToken = {}
        userToken.access_token = response.data.access_token
        userToken.refresh_token = response.data.refresh_token
        userToken.expiration = moment.unix(parseInt(new Date(Date()).getTime() / 1000) + parseInt(response.data.expires_in)).format('YYYY-MM-DD HH:mm:ss')
        this.$http.get(apiDomain + 'account', {headers: getHeader(userToken)}).then(response => {
          if (!response.data.permission.is_admin && !response.data.site_options.enable_panel_login) {
            this.enable_panel_login = response.data.site_options.enable_panel_login
            this.httpRequest.httpCode = 0
            this.httpRequest.message = 'Maintenance Mode.'
            this.setOnLogin()
          } else if (response.data.permission.update_account) {
            this.enable_panel_login = response.data.site_options.enable_panel_login
            // userToken.id = response.data.id
            // userToken.username = response.data.username
            this.$auth.setUserToken(userToken)
            this.httpRequest.httpCode = 200
            this.httpRequest.process = false
            if (this.$auth.isAuth()) {
              var debounceLoginRedirection = _.debounce(() => {
                this.$store.dispatch('setUserToken', this.$auth.getUserToken())
                this.$router.push({name: 'account-profile'})
              }, 1000)
              debounceLoginRedirection()
            }
          } else {
            this.httpRequest.httpCode = 0
            this.httpRequest.message = 'User is currently ' + response.data.profile.status.name_get + '.'
            this.setOnLogin()
          }
          this.password = ''
        }).catch(error => {
          if (error.response) {
            this.httpRequest.httpCode = error.response.status
            if (error.response.data.message) {
              this.httpRequest.message = error.response.data.message
            } else {
              this.httpRequest.message = error.response.statusText
            }
          } else {
            this.httpRequest.httpCode = 0
            this.httpRequest.message = error.message
          }
          this.setOnLogin()
        })
      }).catch(error => {
        if (error.response) {
          console.log(error.response)
          this.httpRequest.httpCode = error.response.status
          if (error.response.data.message) {
            this.httpRequest.message = error.response.data.message
          } else {
            this.httpRequest.message = error.response.statusText
          }
        } else {
          this.httpRequest.httpCode = 0
          this.httpRequest.message = error.message
        }
        this.setOnLogin()
      })
    },

    setOnLogin () {
      var debounceLoginRedirection = _.debounce(() => {
        this.httpRequest.process = false
      }, 1000)
      debounceLoginRedirection()
    }

  }
}
</script>



