<template>
    <div>
        <div v-if="!httpLoad.process && httpLoad.access && !enable_panel_login" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                Maintenance Mode.
            </div>
        </div>

        <div v-if="httpLoad.process" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="!httpLoad.process && !httpLoad.access" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="httpLoad.access">

            <div class="page-header">
                <h2>{{ langStore.language[30] }}</h2>
            </div>

            <div class="row">

                <div class="col-md-12">

                    <div class="panel panel-default">

                        <div class="panel-heading">
                            <h3 class="panel-title">{{ langStore.language[32] }}</h3>
                        </div>

                        <div class="panel-body">

                            <div v-if="httpRequest.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="success">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                    <ul>
                                        <li v-for="voucher in vouchers">
                                            {{ voucher }}
                                        </li>
                                    </ul>
                                </div>
                            </div>

                            <form class="form-horizontal" v-on:submit.prevent="">

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Account Credits :</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static">
                                            <template v-if="authUserStore.profile.credits==='No Limit'">
                                                <span class="label label-success">{{ authUserStore.profile.credits }}</span>
                                            </template>
                                            <template v-else>
                                                <span :class="{'label ': true, 'label-primary': authUserStore.profile.credits > 0, 'label-danger': authUserStore.profile.credits === 0}">{{ authUserStore.profile.credits }}</span>
                                            </template>
                                        </p>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.credits}">
                                    <label for="credits" class="col-sm-3 control-label">Bulk Generate</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="credits" name="credits" v-model.trim="input.credits" placeholder="1 Voucher = 1 Credit">
                                        <small v-if="!process_loader" v-for="error in inputErrors.credits" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <div class="col-sm-offset-3 col-sm-9">
                                        <button type="submit" class="btn btn-primary" :disabled="process_loader" data-toggle="modal" data-target="#generateVoucher">Generate</button>
                                    </div>
                                </div>

                            </form>

                        </div><!-- end .panel-body -->

                    </div><!-- end .panel .panel-default -->



                    <div class="panel panel-default">

                        <div class="panel-heading">
                            <h3 class="panel-title">{{ langStore.language[33] }}</h3>
                        </div>

                        <div class="panel-body">


                            <form class="form-inline" v-on:submit.prevent="">
                                <div class="form-group">
                                    <label>Search</label>
                                </div>

                                <div class="form-group">
                                    <input type="text" class="form-control"
                                           placeholder="Search"
                                           v-model="query.search_input"
                                           @keyup.enter="fetchInitSearch()" :disabled="voucher_loader">
                                </div>

                            </form>

                            <div>&nbsp;</div>

                            <div v-if="authUserStore.permission.is_admin" class="btn-group" role="group" aria-label="...">
                                <button :disabled="model.data && model.data.length === 0 || httpVoucher.process" type="button" class="btn btn-danger" data-toggle="modal" data-target="#deleteVoucher">Delete</button>
                                <div>&nbsp;</div>
                            </div>

                            <div v-if="httpVoucher.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpVoucher.message }}
                                </div>
                            </div>

                            <div v-if="voucher_trigger && voucher_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsgVoucher"><span>&times;</span></button>
                                    {{ httpVoucher.message }}
                                </div>
                            </div>

                            <div v-if="voucher_trigger && !voucher_status.error" class="loading">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsgVoucher"><span>&times;</span></button>
                                    {{ httpVoucher.message }}
                                </div>
                            </div>

                            <div class="table-responsive" v-if="httpVoucher.httpCode === 200">
                                <table class="table table-striped table-condensed table-hover">
                                    <thead>
                                    <tr>
                                        <th v-if="authUserStore.permission.is_admin"><input type="checkbox" v-model="selectAll"></th>
                                        <th v-for="column in columns" @click="toggleOrder(column)">
                                            <span :class="{'label label-default' : column === query.column}">{{ column_transformer(column) }}</span>
                                            <span v-if="column === query.column">
                                                <i v-if="query.direction === 'desc'" class="fa fa-sort-amount-desc"></i>
                                                <i v-else class="fa fa-sort-amount-asc"></i>
                                            </span>
                                        </th>
                                        <th>Used By</th>
                                        <th>User Created</th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    <tr v-for="row in model.data">
                                        <td v-if="authUserStore.permission.is_admin"><input type="checkbox" v-model="selected" :value="row.id" number></td>
                                        <td>{{ row.code }}</td>
                                        <td>{{ row.duration }}</td>
                                        <td>{{ row.updated_at }}</td>
                                        <td>{{ row.created_at }}</td>
                                        <td>{{ row.applied_to }}</td>
                                        <td>{{ row.created_by }}</td>
                                    </tr>
                                    <tr v-if="model.data && model.data.length === 0">
                                        <td colspan="8">No results</td>
                                    </tr>
                                    </tbody>
                                    <tfoot>
                                    <tr>
                                        <td colspan="8">

                                            <form class="form-inline" v-on:submit.prevent="">
                                                <div class="form-group">
                                                    <span>Displaying {{model.from}} - {{model.to}} of {{model.total}} rows Current Page: {{ model.current_page }} | </span>
                                                </div>

                                                <div class="form-group">
                                                    <button class="btn btn-default" @click="prev()">&laquo;</button>
                                                </div>
                                                <div class="form-group">
                                                    <button class="btn btn-default" @click="next()">&raquo;</button>
                                                </div>
                                            </form>
                                        </td>
                                    </tr>
                                    </tfoot>
                                </table>

                            </div>


                        </div><!-- end .panel-body -->

                    </div><!-- end .panel .panel-default -->

                </div><!-- end .col-md-9 -->

            </div><!-- end .row -->

        </div><!-- end v-if="!this.fetchUserDataStatus.loading && !this.fetchUserDataStatus.error && permission.update_account" -->
        <top-progress ref="topProgress"></top-progress>

        <!-- Small modal -->
        <div class="modal fade" id="generateVoucher" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel1">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>
                            Generate voucher?
                        </p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="generatevoucher_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Small modal -->
        <div class="modal fade" id="deleteVoucher" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel2">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>
                            Delete vouchers?
                        </p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="deletevoucher_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<style>
</style>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {apiDomain, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default {
  mounted () {
    this.$refs.topProgress.start()
  },
  created () {
    // console.log('Generate Voucher created.')
    this.loadData()
  },

  components: {
    SideMenu,
    topProgress
  },

  computed: {
    selectAll: {
      get: function () {
        return this.model.data ? this.selected.length === this.model.data.length : false
      },
      set: function (value) {
        var selected = []

        if (value) {
          this.model.data.forEach(function (user) {
            selected.push(user.id)
          })
        }

        this.selected = selected
      }
    },

    process_loader () {
      return this.httpRequest.process
    },

    process_trigger () {
      return !this.process_loader && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.process_loader && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    voucher_loader () {
      return this.httpVoucher.process
    },

    voucher_trigger () {
      return !this.voucher_loader && this.httpVoucher.trigger
    },

    voucher_status () {
      return {
        error: !this.voucher_loader && this.httpVoucher.httpCode !== 200,
        message: this.httpVoucher.message
      }
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      siteOptionsStore: state => state.siteOptionsStore,
      langStore: state => state.langStore
    })
  },

  data () {
    return {
      httpLoad: {
        process: true,
        httpCode: 0,
        access: false,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      httpVoucher: {
        process: false,
        httpCode: 200,
        trigger: null,
        message: null
      },
      inputErrors: [],
      input: {
        credits: null,
        duration_days: 0,
        duration_hours: 0
      },
      vouchers: [],
      model: {},
      columns: {},
      query: {
        page: 1,
        column: 'id',
        direction: 'desc',
        per_page: 20,
        search_column: 'code',
        search_operator: 'equal',
        search_input: ''
      },
      operators: {
        equal: '='
      },
      selected: [],
      enable_panel_login: false
    }
  },

  head: {
    // To use "this" in the component, it is necessary to return the object through a function
    title: function () {
      return {
        inner: this.$store.state.siteOptionsStore.sub_name,
        separator: this.$store.state.siteOptionsStore.separator,
        complement: this.$store.state.siteOptionsStore.site_name
      }
    }
  },

  methods: {
    hideMsg () {
      this.httpRequest.trigger = false
      this.inputErrors = []
    },

    hideMsgVoucher () {
      this.httpVoucher.trigger = false
    },

    generatevoucher_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Generating voucher...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.generate_voucher(), 500)
        debounce()
      }
    },

    generate_voucher () {
      var query = {
        page: 1,
        column: 'id',
        direction: 'desc',
        per_page: 20,
        search_column: 'code',
        search_operator: 'equal',
        search_input: '',
        credits: this.input.credits
      }
      this.$http.post(apiDomain + 'voucher/generate', query, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        // console.log(response)

        var debounce = _.debounce(() => {
          this.input = {}

          this.$store.dispatch('setAccountProfile', response.data.profile)

          this.vouchers = response.data.voucher

          this.model = response.data.model
          this.columns = response.data.columns

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            errorMessage = 'Input Error.'
            this.inputErrors = error.response.data
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    loadData () {
      this.httpLoad.message = 'Accessing Page...'
      this.$http.get(apiDomain + 'voucher/generate' + `?column=${this.query.column}&direction=${this.query.direction}&page=${this.query.page}&per_page=${this.query.per_page}&search_column=${this.query.search_column}&search_operator=${this.query.search_operator}&search_input=${this.query.search_input}`, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        this.$store.dispatch('setSiteName', response.data.site_options.site_name)
        this.$store.dispatch('setSubName', response.data.site_options.sub_name)
        this.$store.dispatch('setSeparator', '-')
        this.$emit('updateHead')

        this.$store.dispatch('setLanguage', response.data.language)
        this.$store.dispatch('setAccountProfile', response.data.profile)
        this.$store.dispatch('setAccountPermission', response.data.permission)

        var debounce = _.debounce(() => {
          this.$refs.topProgress.done()

          this.enable_panel_login = response.data.site_options.enable_panel_login

          this.model = response.data.model
          this.columns = response.data.columns

          this.httpLoad.httpCode = response.status
          this.httpLoad.access = true
          this.httpLoad.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        var sitename = 'VPN Panel'
        var subname = ' '
        var separator = ' '
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 403 && error.response.data.message) {
            errorMessage = error.response.data.message
            this.$store.dispatch('setAccountProfile', error.response.data.profile)
            this.$store.dispatch('setAccountPermission', error.response.data.permission)

            sitename = error.response.data.site_options.site_name
            subname = error.response.data.site_options.sub_name
            separator = '-'
          }
        }

        this.$store.dispatch('setSiteName', sitename)
        this.$store.dispatch('setSubName', subname)
        this.$store.dispatch('setSeparator', separator)
        this.$emit('updateHead')

        var debounce = _.debounce(() => {
          this.$refs.topProgress.fail()
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 1000)
        debounce()
      })
    },

    column_transformer (col) {
      if (col === 'code') {
        return 'Voucher Code'
      }
      if (col === 'duration') {
        return 'Duration'
      }
      if (col === 'updated_at') {
        return 'Date Used'
      }
      if (col === 'created_at') {
        return 'Date Created'
      }
      if (col === 'user_created') {
        return 'User Created'
      }
    },

    next () {
      if (this.model.next_page_url) {
        this.query.page++
        this.httpVoucher.process = true
        this.fetchSearchData()
      }
    },

    prev () {
      if (this.model.prev_page_url) {
        this.query.page--
        this.httpVoucher.process = true
        this.fetchSearchData()
      }
    },

    toggleOrder (column) {
      if (column === this.query.column) {
        // only change direction
        if (this.query.direction === 'desc') {
          this.query.direction = 'asc'
        } else {
          this.query.direction = 'desc'
        }
      } else {
        this.query.column = column
        this.query.direction = 'asc'
      }
      this.httpVoucher.process = true
      this.fetchSearchData()
    },

    fetchInitSearch () {
      if (!this.voucher_loader) {
        this.httpVoucher.process = true
        this.query.page = 1
        this.fetchSearchData()
      }
    },

    fetchSearchData () {
      this.selected = []
      this.httpVoucher.message = 'Fetching data...'
      this.httpVoucher.process = true
      this.httpVoucher.trigger = true
      this.$http.get(apiDomain + 'voucher/generate' + `?column=${this.query.column}&direction=${this.query.direction}&page=${this.query.page}&per_page=${this.query.per_page}&search_column=${this.query.search_column}&search_operator=${this.query.search_operator}&search_input=${this.query.search_input}`, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        // console.log(response.data.model)

        var debounce = _.debounce(() => {
          this.model = response.data.model
          this.columns = response.data.columns
          this.httpVoucher.httpCode = response.status
          this.httpVoucher.process = false
          this.httpVoucher.trigger = false
        }, 1000)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    deletevoucher_trigger () {
      if (!this.process_loader) {
        this.httpVoucher.message = 'Deleting voucher...'
        this.httpVoucher.trigger = true
        this.httpVoucher.process = true
        var debounce = _.debounce(() => this.delete_voucher(), 500)
        debounce()
      }
    },

    delete_voucher () {
      var query = {
        id: this.selected,
        page: this.query.page,
        column: this.query.column,
        direction: this.query.direction,
        per_page: this.query.per_page,
        search_column: this.query.search_column,
        search_operator: this.query.search_operator,
        search_input: this.query.search_input
      }
      this.$http.post(apiDomain + 'voucher/delete-voucher', query, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        // console.log(response)

        var debounce = _.debounce(() => {
          this.selected = []

          this.model = response.data.model
          this.columns = response.data.columns

          this.httpVoucher.httpCode = response.status
          this.httpVoucher.message = response.data.message
          this.httpVoucher.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        // console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpVoucher.httpCode = errorHttpCode
          this.httpVoucher.message = errorMessage
          this.httpVoucher.process = false
        }, 1000)
        debounce()
      })
    }
  }
}
</script>



