const state = {
  site_name: 'Loading...',
  sub_name: ' ',
  separator: ' ',
  public_online_users: false,
  public_credit_distributors: false,
  public_server_status: false
}

const mutations = {
  SITE_NAME (state, sitename) {
    state.site_name = sitename
  },
  SUB_NAME (state, subname) {
    state.sub_name = subname
  },
  SEPARATOR (state, separator) {
    state.separator = separator
  },
  PUBLIC_ONLINE_USERS (state, publiconlineusers) {
    state.public_online_users = publiconlineusers
  },
  PUBLIC_CREDIT_DISTRIBUTORS (state, publiccreditdistributors) {
    state.public_credit_distributors = publiccreditdistributors
  },
  PUBLIC_SERVER_STATUS (state, publicserverstatus) {
    state.public_server_status = publicserverstatus
  }
}

const actions = {
  setSiteName ({commit}, sitename) {
    commit('SITE_NAME', sitename)
  },
  setSubName ({commit}, subname) {
    commit('SUB_NAME', subname)
  },
  setSeparator ({commit}, separator) {
    commit('SEPARATOR', separator)
  },
  setPublicOnlineUsers ({commit}, publiconlineusers) {
    commit('PUBLIC_ONLINE_USERS', publiconlineusers)
  },
  setPublicCreditDistributors ({commit}, publiccreditdistributors) {
    commit('PUBLIC_CREDIT_DISTRIBUTORS', publiccreditdistributors)
  },
  setPublicServerStatus ({commit}, publicserverstatus) {
    commit('PUBLIC_SERVER_STATUS', publicserverstatus)
  }
}

export default {
  state, mutations, actions
}



