const state = {
  language: [
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''}
  ]
}

const mutations = {
  LANGUAGE (state, language) {
    state.language = language
  }
}

const actions = {
  setLanguage ({commit}, language) {
    commit('LANGUAGE', language)
  }
}

export default {
  state, mutations, actions
}



