<template>
    <div>
        <div class="col-md-3">
            <div class="list-group">
                <router-link class="list-group-item" :class="{'active' : $route.name === 'voucher-apply'}" :to="{name: 'voucher-apply'}">Apply Voucher</router-link>
                <router-link v-if="authUserStore.permission.generate_voucher && authUserStore.profile.user_group_id < 5" class="list-group-item" :class="{'active' : $route.name === 'voucher-generate'}" :to="{name: 'voucher-generate'}">Generate Voucher</router-link>
            </div>
        </div><!-- end .col-md-3 -->
    </div>
</template>

<style>
</style>

<script>
import {mapState} from 'vuex'
export default{
  computed: {
    ...mapState({
      authUserStore: state => state.authUserStore,
      langStore: state => state.langStore
    })
  }
}
</script>



