<template>
    <div>
        <div class="col-md-3">
            <div class="list-group">
                <router-link v-if="authUserStore.permission.is_admin" class="list-group-item" :class="{'active' : $route.name === 'create-post'}" :to="{name: 'create-post'}">New Post</router-link>
                <router-link class="list-group-item" :class="{'active' : $route.name === 'home'}" :to="{name: 'home'}">News &amp; Updates</router-link>
            </div>
        </div><!-- end .col-md-3 -->
    </div>
</template>

<style>
</style>

<script>
import {mapState} from 'vuex'
export default{
  computed: {
    ...mapState({
      authUserStore: state => state.authUserStore,
      langStore: state => state.langStore
    })
  }
}
</script>



