<template>
    <div>
        <nav class="navbar navbar-default">
            <!-- Brand and toggle get grouped for better mobile display -->
            <div class="navbar-header">
                <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
                    <span class="sr-only">Toggle navigation</span>
                    <span class="icon-bar"></span>
                    <span class="icon-bar"></span>
                    <span class="icon-bar"></span>
                </button>
                <template v-if="authUserStore.userToken !== null">
                    <router-link class="navbar-brand" :to="{name: 'home'}"><b>{{ siteOptionsStore.site_name }}</b></router-link>
                </template>
                <template v-else>
                    <router-link class="navbar-brand" :to="{name: 'public-home'}"><b>{{ siteOptionsStore.site_name }}</b></router-link>
                </template>
            </div>

            <!-- Collect the nav links, forms, and other content for toggling -->
            <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
                <ul class="nav navbar-nav">
                    <li v-if="authUserStore.userToken === null" :class="{'active' : $route.name === 'login'}"><router-link :to="{name: 'login'}">Login</router-link></li>
                    <li v-if="authUserStore.userToken === null && siteOptionsStore.public_credit_distributors" :class="{'active' : $route.name === 'public-credit-distributors'}"><router-link :to="{name: 'public-credit-distributors'}">{{ langStore.language[21] }}</router-link></li>
                    <li v-if="authUserStore.userToken === null && siteOptionsStore.public_online_users" :class="{'active' : $route.name === 'public-online-users'}"><router-link :to="{name: 'public-online-users'}">{{ langStore.language[23] }}</router-link></li>
                    <li v-if="authUserStore.userToken === null && siteOptionsStore.public_server_status" :class="{'active' : $route.name === 'public-server-status'}"><router-link :to="{name: 'public-server-status'}">{{ langStore.language[27] }}</router-link></li>
                    <li v-if="authUserStore.userToken !== null && langStore.language !== null" :class="{'active' : $route.name === 'account-profile' || $route.name === 'account-security' || $route.name === 'extend-duration' || $route.name === 'transfer-credit' || $route.name === 'vacation-mode' || $route.name === 'account-vpn'}"><router-link :to="{name: 'account-profile'}">{{ langStore.language[0] }}</router-link></li>
                    <li v-if="authUserStore.userToken !== null && authUserStore.permission.manage_user" :class="{'active' : $route.name === 'manageuser-all' || $route.name === 'manageuser-ultimate' || $route.name === 'manageuser-premium' || $route.name === 'manageuser-reseller' || $route.name === 'manageuser-client' || $route.name === 'user-profile' || $route.name === 'user-security' || $route.name === 'manageuser-create'}"><router-link :to="{name: 'manageuser-client'}">{{ langStore.language[5] }}</router-link></li>
                    <li v-if="authUserStore.userToken !== null" :class="{'active' : $route.name === 'credit-distributors'}"><router-link :to="{name: 'credit-distributors'}">{{ langStore.language[21] }}</router-link></li>
                    <li v-if="authUserStore.userToken !== null" :class="{'active' : $route.name === 'online-users'}"><router-link :to="{name: 'online-users'}">{{ langStore.language[23] }}</router-link></li>
                    <li v-if="authUserStore.userToken !== null" :class="{'active' : $route.name === 'server-status'}"><router-link :to="{name: 'server-status'}">{{ langStore.language[27] }}</router-link></li>
                    <li v-if="authUserStore.userToken !== null && authUserStore.permission.is_admin || authUserStore.permission.manage_vpn_server || authUserStore.permission.manage_voucher" :class="{'dropdown': true, 'active': $route.name === 'admin-settings' || $route.name === 'voucher-generate' || $route.name === 'vpnserver-list' || $route.name === 'vpnserver-add'}">
                        <a href="#" class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">{{ langStore.language[28] }} <span class="caret"></span></a>
                        <ul class="dropdown-menu">
                            <li v-if="authUserStore.permission.is_admin" :class="{'active' : $route.name === 'admin-settings'}"><router-link :to="{name: 'admin-settings'}">{{ langStore.language[29] }}</router-link></li>
                            <li v-if="authUserStore.permission.is_admin || authUserStore.permission.manage_voucher" :class="{'active' : $route.name === 'voucher-generate'}"><router-link :to="{name: 'voucher-generate'}">{{ langStore.language[30] }}</router-link></li>
                            <li v-if="authUserStore.permission.is_admin || authUserStore.permission.manage_vpn_server" :class="{'active' : $route.name === 'vpnserver-list' || $route.name === 'vpnserver-add'}"><router-link :to="{name: 'vpnserver-list'}">{{ langStore.language[31] }}</router-link></li>
                            <li v-if="authUserStore.permission.is_admin" :class="{'active' : $route.name === 'admin-credit-transfer-logs'}"><router-link :to="{name: 'admin-credit-transfer-logs'}">{{ langStore.language[34] }}</router-link></li>
                        </ul>
                    </li>
                </ul>
                <ul class="nav navbar-nav navbar-right" v-if="authUserStore.userToken !== null && authUserStore.userToken.access_token">
                    <li class="dropdown">
                        <a href="#" class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">Account <span class="caret"></span></a>
                        <ul class="dropdown-menu">
                            <li><router-link :to="{name: 'account-profile'}">Signed is as {{ authUserStore.profile.username }}</router-link></li>
                            <li role="separator" class="divider"></li>
                            <li><a href="#" v-on:click.prevent="logout" class="logout-class">Logout</a></li>
                        </ul>
                    </li>
                </ul>
            </div><!-- /.navbar-collapse -->
        </nav>

    </div>

</template>

<script>
import {mapState} from 'vuex'
export default{
  computed: {
    ...mapState({
      authUserStore: state => state.authUserStore,
      siteOptionsStore: state => state.siteOptionsStore,
      langStore: state => state.langStore
    })
  },

  methods: {
    logout () {
      this.$auth.destroyUser()
      this.$store.dispatch('clearAuthUser')
      this.$router.push({name: 'login'})
    }
  }
}
</script>



