<template>
    <div>
        <div class="col-md-3">
            <div class="list-group">
                <router-link class="list-group-item" :class="{'active' : $route.name === 'public-home' || $route.name === 'public-post-item'}" :to="{name: 'public-home'}">News &amp; Updates</router-link>
            </div>
        </div><!-- end .col-md-3 -->
    </div>
</template>

<style>
</style>

<script>
import {mapState} from 'vuex'
export default{
  computed: {
    ...mapState({
      authUserStore: state => state.authUserStore,
      langStore: state => state.langStore
    })
  }
}
</script>



