<template>
    <div>
        <div class="col-md-3">
            <div class="list-group">
                <router-link class="list-group-item" :class="{'active' : $route.name === 'manageuser-create'}" :to="{name: 'manageuser-create'}">{{ langStore.language[22] }}</router-link>
                <router-link v-if="authUserStore.profile.user_group_id < 4" :class="{'list-group-item': true, 'active': $route.name === 'manageuser-all'}" :to="{name: 'manageuser-all'}">{{ langStore.language[6] }}</router-link>
                <router-link v-if="authUserStore.permission.is_admin" :class="{'list-group-item': true, 'active': $route.name === 'manageuser-ultimate'}" :to="{name: 'manageuser-ultimate'}">{{ langStore.language[7] }}</router-link>
                <router-link v-if="authUserStore.profile.user_group_id < 3" :class="{'list-group-item': true, 'active': $route.name === 'manageuser-premium'}" :to="{name: 'manageuser-premium'}">{{ langStore.language[8] }}</router-link>
                <router-link v-if="authUserStore.profile.user_group_id < 4" :class="{'list-group-item': true, 'active': $route.name === 'manageuser-reseller'}" :to="{name: 'manageuser-reseller'}">{{ langStore.language[9] }}</router-link>
                <router-link v-if="authUserStore.profile.user_group_id < 5" :class="{'list-group-item': true, 'active': $route.name === 'manageuser-client'}" :to="{name: 'manageuser-client'}">{{ langStore.language[10] }}</router-link>
                <router-link v-if="authUserStore.permission.is_admin" :class="{'list-group-item': true, 'active': $route.name === 'manageuser-trash'}" :to="{name: 'manageuser-trash'}">{{ langStore.language[24] }}</router-link>
            </div>
        </div><!-- end .col-md-3 -->
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default{
  computed: {
    ...mapState({
      authUserStore: state => state.authUserStore,
      langStore: state => state.langStore
    })
  }
}
</script>



