<template>
    <div>
        <div class="col-md-3">
            <div class="list-group">
                <router-link class="list-group-item" :class="{'active' : $route.name === 'account-profile'}" :to="{name: 'account-profile'}">{{ langStore.language[0] }}</router-link>
                <router-link class="list-group-item" :class="{'active' : $route.name === 'account-security'}" :to="{name: 'account-security'}">{{ langStore.language[1] }}</router-link>
                <router-link class="list-group-item" :class="{'active' : $route.name === 'extend-duration'}" :to="{name: 'extend-duration'}">{{ langStore.language[2] }}</router-link>
                <router-link v-if="authUserStore.permission.is_admin || (authUserStore.permission.manage_user && authUserStore.profile.distributor)" class="list-group-item" :class="{'active' : $route.name === 'transfer-credit'}" :to="{name: 'transfer-credit'}">{{ langStore.language[3] }}</router-link>
                <router-link v-if="authUserStore.profile.user_group_id==6" class="list-group-item" :class="{'active' : $route.name === 'vacation-mode'}" :to="{name: 'vacation-mode'}">Vacation Mode</router-link>
                <router-link class="list-group-item" :class="{'active' : $route.name === 'account-vpn'}" :to="{name: 'account-vpn'}">{{ langStore.language[4] }}</router-link>
                <router-link class="list-group-item" :class="{'active' : $route.name === 'account-credit-transfer-logs'}" :to="{name: 'account-credit-transfer-logs'}">Credit Transfer Logs</router-link>
            </div>
        </div><!-- end .col-md-3 -->
    </div>
</template>

<style>
</style>

<script>
import {mapState} from 'vuex'
export default{
  computed: {
    ...mapState({
      authUserStore: state => state.authUserStore,
      langStore: state => state.langStore
    })
  }
}
</script>



